<template>
    <!--    <div :class="[this.imageID,'image-window']" v-bind:class="{visible: stateOpen}">-->
    <div v-bind:class="['window-'+this.windowsImageObject.imageID,'image-window']">

        <div class="image-window-content">
            <div class="image-window-closebutton" v-on:click="closeButtonClicked">
                X
            </div>

            <video class="image" v-if="this.windowsImageObject.typeVideo" :id="'image-'+this.windowsImageObject.imageID" :src="this.imageURL" alt="" autoplay loop></video>
            <img class="image" v-else :id="'image-'+this.windowsImageObject.imageID" :src="this.imageURL" alt="">
        </div>

    </div>
</template>

<script>
    import interact from 'interactjs'
    import _ from 'lodash'

    export default {
        name: "ImageWindow",
        // props: ['imageID'],
        props: ['windowsImageObject'],

        mounted() {
            const position = {x: this.windowsImageObject.posX - (550 / 2), y: this.windowsImageObject.posY}
            var windowInDom = document.querySelector('.window-' + this.windowsImageObject.imageID)
            windowInDom.style.transform = `translate(${position.x}px, ${position.y}px)`
            var imageOfWindow = document.querySelector('#image-' + this.windowsImageObject.imageID)
            console.log('imageofWindow', imageOfWindow)
            console.log('imageofWindownatheight', imageOfWindow.naturalHeight)

            var imageAspectRatio = this.windowsImageObject.width / this.windowsImageObject.height
            console.log(imageAspectRatio)
            windowInDom.style.height = '700px'
            windowInDom.style.width = (700 * imageAspectRatio) + 'px'

            interact('.window-' + this.windowsImageObject.imageID)
                .draggable({
                    listeners: {
                        start(event) {
                            console.log(position)
                        },
                        move(event) {
                            position.x += event.dx
                            position.y += event.dy
                            event.target.style.transform =
                                `translate(${position.x}px, ${position.y}px)`

                        },
                    },
                    modifiers: [
                        interact.modifiers.snap({
                            targets: [
                                interact.createSnapGrid({x: 15, y: 15})
                            ],
                            range: Infinity,
                            relativePoints: [{x: 0, y: 0}]
                        }),
                        interact.modifiers.restrict({
                            // restriction: element.parentNode,
                            elementRect: {top: 0, left: 0, bottom: 1, right: 1},
                            endOnly: true
                        })
                    ],
                }).resizable({
                edges: {

                    bottom: true,
                    right: true
                }
                ,
                preserveAspectRatio: true,
                modifiers: [

                    interact.modifiers.snap({
                        targets: [
                            interact.createSnapGrid({x: 15, y: 15})
                        ],
                        range: Infinity,
                        relativePoints: [{x: 0, y: 0}]
                    }),
                    interact.modifiers.aspectRatio({
                        ratio: 'preserve'
                    }),


                ],
            })
                .on('resizemove', event => {
                    let {x, y} = event.target.dataset


                    x = position.x
                    y = position.y

                    Object.assign(event.target.style, {
                        width: `${event.rect.width}px`,
                        height: `${event.rect.height}px`,
                        // transform: `translate(${event.deltaRect.left}px, ${event.deltaRect.top}px)`
                    })

                    Object.assign(event.target.dataset, {x, y})
                }).on('dragend', event => {


                this.$store.commit('updateWindowCoordinates', {
                    imageID: this.windowsImageObject.imageID,
                    coordinates: {
                        x: event.rect.left,
                        y: event.rect.top

                    }

                })

            })


        },
        computed: {
            stateOpen: function () {

                // var imageWindowInCollection = _.find(this.$store.state.imageWindows, {'imageID': this.imageID});
                // console.log(imageWindowInCollection.stateOpen)
                // return imageWindowInCollection.stateOpen

                return this.windowsImageObject.stateOpen

            },
            coordinates: function () {
                // var imageWindowInCollection = _.find(this.$store.state.imageWindows, {'imageID': this.imageID});
                // var coordinates = {
                //     posX: imageWindowInCollection.posX,
                //     posY: imageWindowInCollection.posY,
                // }
                // return coordinates
                return this.windowsImageObject.coordinates

            },
            initCoordinates: function () {
                return this.$store.state.activeImageWindows
            },
            imageURL: function () {
                if (this.windowsImageObject.typeVideo) {
                    return "/images/imageboxes/" + this.windowsImageObject.imageID + ".mp4"

                } else {
                    return "/images/imageboxes/" + this.windowsImageObject.imageID + ".jpg"

                }

            }
        },
        methods: {
            closeButtonClicked: function () {
                this.$store.commit('closeWindow', this.windowsImageObject.imageID)

            }
        }
    }
</script>

<style scoped>
    .image-window {
        border-width: 4px;
        border-style: double;
        border-color: black;

        position: absolute;
        background-color: white;
        top: 0;
        left: 0;
        touch-action: none;
        box-sizing: border-box;
        color: white;
    }


    .image-window-content {
        position: relative;
        width: 100%;
        height: 100%;
    }

    .image-window-closebutton {
        position: absolute;
        top: 0;
        right: 0;
        font-size: 18px;
        cursor: pointer;
    }

    .image {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
</style>