import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import DocumentTurkish from "../views/DocumentTurkish";
import DocumentEnglish from "../views/DocumentEnglish";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home,

    },
    {
        path: '/about',
        name: 'About',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/tr',
        name: 'DocumentTurkish',
        component: DocumentTurkish
    },
    {
        path: '/en',
        name: 'DocumentEnglish',
        component: DocumentEnglish
    },

]

const router = new VueRouter({
    routes
})

export default router
