var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"document"},[_vm._l((_vm.activeImageWindows),function(activeImageWindow){return _c('image-window',{key:activeImageWindow.imageID,attrs:{"windows-image-object":activeImageWindow}},[_vm._v(_vm._s(activeImageWindow))])}),_c('div',{attrs:{"id":"section-1"}}),_c('span',[_vm._v("Saat 7:05, dışarısı serin, çatıların üzerindeki parlaklık dökülen çiğden olsa gerek. Camın ardından dışarıyı net göremiyorum, düne kıyasla hava değişikliği camın yüzeyini buğulaştırmış. Güne, camın üzerindeki bulutsu buğuyu silerek başladım. Pencereyi aralayıp görüntü alabilsem de, çevreyi genişçe bir alanı saydamlaştırıp takip etmek istemiştim. 7:00’da uyanmamın sebebi, cezaevindeki birçok kişinin bu saatlerde güne başladığını çok da güvenilir olmayan bir kaynaktan okumamdı. Odağımı yarı uykulu bir halde gezdirmeye başladım, manzaramın kapsadığı sınırları test ettiğim bir gezintiydi bu. Otoyol, metro durakları, bu yolların bağlandığı büyük bir viyadük, ufukta yükselen yeşil bir tepe ve tepenin çevresinde sıralanan farklı yükseklikte "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0516'}},[_vm._v("konutlar")]),_vm._v(" vardı. Bulunduğum daireden yolun ayırdığı cezaevinin sınırlarını görebiliyordum. Dikdörtgen şeklindeki uzun ince bir alanı kapsayan bu arazinin kuzey ucu dürbünümle ancak fark edilebiliyordu. Güney ucu ise odamın paralelinde, ayaklarımın altına kadar uzayan arazinin bir diğer sınırıydı. Çift çeperle çevrilmiş dış duvar ve her bir koğuşun bağlı olduğu avluların duvarları oldukça yüksek. Bulunduğum noktada bu yüksekliğin çok önemi yok. Bu noktadan 8 avluyu, bina girişinin önündeki büyük açıklığı, bu açıklığın bağlandığı araç girişini görebiliyordum. Avlular benden uzaklaştıkça içerilerini görmek gittikçe zorlaşıyor ve uçlara doğru kendini tekrar eden köşeli örüntülere dönüşüyorlardı. Odağımı gezdirmeye başladıktan yaklaşık 15 dakika sonra dış duvara odaklandığımda mavi üniformalı "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1926'}},[_vm._v("iki gardiyan")]),_vm._v(" gördüm. Yapının çevresinde devriye turları olması olağandı. Takip ettikleri rota, arazinin en dış sınırını belirleyen duvarla, onu hemen içerisinden takip eden ikinci bir duvarın arasındaki alandı. İki katmana sahip olan bu yapı, arasında sağladığı yaklaşık 3-4 metrelik boşlukta sirkülasyona izin veriyor. Saat 7:40, devriyeyi gördükten sonra etrafta bir hareketlilik görmedim. Gördüğüm 5 gözcü kulesinin her birinin içinde birer gardiyan duruyor. Çevredeki sakinlik devam ederken güneş gittikçe kendini gösterdi, her bir avlunun içerisine yavaşça giren ışık, sıralanan bu dikdörtgen alanların içine doğru keskin çizgiler çizerek gittikçe dikleşti. Her avludaki gölge birbirini taklit ediyordu. 7:46’da biri, araç girişinin yanındaki kapıdan geçerek binanın giriş kapısına ulaştı, elinde poşet taşıyordu. Hava bütünüyle aydınlandığı için artık gözlem kulelerinin içlerini daha net görmeye başlamıştım. 30 dakikalık aralıkta içeri 4 kişi daha girdi. Yakın aralıklarla yapılan bu girişlerde tekrar herkesin elinde poşetler gördüm, içeride giyecekleri üniformaları taşıdıklarını düşündüm. Ziyaretçi olamazlardı çünkü internet sitesinde verilen bilgide ziyaretçi girişleri sadece hafta içiydi, hafta sonlarınınsa telefon görüşmelerine ayrılan günler olduğu yazıyordu. Bir arada giriş yapan iki kişiden birinin siyah üniforması, diğerininse siyah-kırmızı çizgili montu vardı. Yolda sohbet ederek içeri girdiler, arkadaş olsalar gerek. Hemen arkalarından kapı kapanmadan yetişen "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0417'}},[_vm._v("koyu yeşil montlu")]),_vm._v(" biri daha içeri girdi. Girenlerin ardından 8:30-8:40 arasında 3 kişi bina kapısından dışarı çıktı, birinin kazağının yeşili çıplak gözle hemen ayırt edilebiliyordu, ellerinde çanta ve poşetler vardı. Bir saat öncesine kadar giriş yapanlara benzemiyorlar, belki de gece nöbetleri bitti ve sabahçılarla yer değiştiriyorlar. Saat 9:00, etraf sakin, ön bahçeye çıkan birkaç kişi oldu. Girişin bulunduğu binada çalışanların hava alması için avlular dışındaki tek yer o bahçe. Avlular sakindi, kimseyi görmemiştim, herkes kahvaltı yapıyor olabilir, ben de acıkmıştım. Mısır gevreği ve süt sabah kahvaltımdı, normalde evdeyken bir yere yetişmem gerekiyorsa mısır gevreği yerim. İlk sabahın heyecanı olsa gerek, gözümü dürbünden ayırmak istemedim. Saat 9:30'da devriye yolunda sivil kıyafetli ve üniformalı iki kişi ilerliyordu. Sivil kıyafetli olanın elinde tutuğu tasmanın diğer ucunda sıkça hareket eden bir köpek vardı. Devriye yolu boyunca köpekle oynayıp ileri geri yarıştılar, köpeğin enerjik hali onu tutanı birden fazla yöne savuruyordu. Bu uzaklıkta odaklanmak, hedefteki hareketliliğin izlerini daha grafikleştiriyor -ikisi de kadrajımda "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0444'}},[_vm._v("eş bulanıklıkta")]),_vm._v(" Bana en yakın gözcü kulesinin altına geldiklerinde kuledeki 2 gardiyan, kulenin balkonundan kenara doğru uzanıp onları gülerek "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0450'}},[_vm._v("izlediler")]),_vm._v(" Devriye gezen gardiyan, kulenin üstünden aşağı doğru yapılan bir konuşmadan sonra aynı rotada yürümeye devam ederken, köpek ve sahibi geldikleri yöne doğru döndüler. Herkes görüntümden çıktığında uzun bir sakinlik araya girdi. Sakinliğin karşısında otoyol, sabah trafiğinden yükselen korna sesleriyle odağımı üzerine çekmişti. Bir süre boyunca işe giden, metroya yetişen ve trafikte ilerleyen kişileri izledim. Saat 11:00’a yakın, 1. avlunun içerisinde bulunan kapı açılıp kapandı, kahverengi kazaklı biri sanki sadece avluyu kontrol edermiş gibi hızlıca avluya girip çıktı. Avluları bana yakınlıklarına göre numaralandırmıştım. 2 hatta bölünmüş 8 avlunun yarısı binanın kuzeyinde, diğer yarısı güneyinde yer alıyordu. Güney tarafında bana yakın avlular sağdan sola doğru 1, 2, 3, 4; kuzey tarafı ise 5, 6, 7, 8’di. Avluların içlerine açılan bir, bazense iki adet kapı bulunuyordu. Bu farklılığın, avluların bağlı olduğu koğuşlarla ilgili olduğunu düşündüm. Metal kapıların bazıları bütünüyle beyaz, geri kalanları ise su yeşili rengindeydi. Genellikle suyun akıntıdan yoksun göletlerde ya da çok dalgalı sahillerde bu renge döndüğünü hatırlıyorum. Avlularda kimseyi görmememin sebebi pazar gününe özgü bir programdan dolayı olabilir. Aynı şekilde binaya giren kişi sayısı yok denecek kadar az. Bu sakinlik içinde avlularda duran nesnelere odaklanıp odağımı gezdirdim. Plastik masalar ve sandalyeler, üzerlerine serilmiş çeşitli desenlerdeki örtüler ve minderler, eş hacimdeki bu alanların her birinde farklı yerlerde ve hallerde sergileniyordu; biri duvara daha yakın ve çiçek desenli minderlerle süslenmiş, bir diğeriyse avlunun tam merkezinde, ekose desenlerle sarılmış olabiliyordu. Odağımı viyadüğe ve ufuktaki tepeye çevirdiğimde, uzayan otoyolların, çeşitlenen binaların, değişen kıyafetlerin sürekli dışa doğru katlandığı bu manzarada, içine ve merkezine doğru katlanan avlulara bakmaktan kendimi alamamıştım. Rüzgâr arttı, bulutlar bu esintiyle birlikte biçim değiştirdi ve güneş belli aralıklarla görülebiliyor. Oradaki yüksek "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0868'}},[_vm._v("duvarlar")]),_vm._v(" rüzgârın büyük bir kısmını kesiyor olsa gerek, bu rezidans dairesinde balkon olmadığı için ne kadar sert estiğini ben de net bir biçimde söyleyemem. Saat 11:20, 2’li gruplar halinde 1. kule ve 2. kule arasında gardiyanlar kesişti, selamlaştılar. Devriyenin görüş alanıma girdiği saatlerin örüntüsü olduğu anlaşılıyor. 1. avludan lacivert kazaklı biri çıkıp koğuşun penceresinden içeri doğru seslendi, bu yaklaşık 2-3 saniye sürdü, merceğimi odaklamaya zamanım olmamıştı. Hemen ardından, 2. avluda uzun saçlı, bıyıklı, orta yaşlı biri duvara yakın bir biçimde ilerlemeye başladı, duvara o kadar yakındı ki belirli aralıklarla saçını görebiliyordum. Avlunun ortasına yaklaştığındaysa üzerindeki kırmızı hırkayı gördüm. Avlunun zeminindeki gri betondan keskin bir biçimde ayrışan parlak kırmızıydı, genellikle spor eşofmanlarında kullanılan kumaş bu ölçüde parlar. Merceğimi yakınlaştırdığımdaysa, eşofmanın omzundan bileğine doğru inen beyaz çizgileri gördüm, çevremizi saran otoyoldaki "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1777'}},[_vm._v("beyaz şeritlere")]),_vm._v(" benziyordu. Bir süre avluda dolaştıktan sonra tekrar duvara yakınlaşarak içeri girdi. Daha dik açıdan görüşümün olması duvarın kenarlarını görmem için bir kolaylıktı -bu dairelerin ortak kullanılan terasından bazı avlular daha dik görülebiliyor. İki farklı açıyı kullanmak için bazen terasa uğrayabileceğimi düşündüm. Saat 12:00 oldu, 1. avlunun kapısı açıldı ve açık bırakıldı. Kapı açıldıktan bir süre sonra yavaşça gözükmeye başlayan tekerlekler, üzerinde konumlanan metal bir iskelet ve yatakla birlikte, bir sedyenin avluya çıkarıldığını anladım. Sedyeyi iten iki kişiden biri siyah üniformalı bir gardiyandı, diğeriyse aynı koğuşu paylaşan mahkûm olabilir. Sedyedeki kişinin başında kırmızı bir bere ve üstünde cezaevi logolu bir battaniye vardı. Diğer mahkûm sedyedekine kıyasla yaşlı biriydi, kahverengi kazak giyiyordu, bu kazağı 1 saat öncesinden hatırlamıştım. Kahverengi kazaklı, sedyeye avlunun kenarına kadar eşlik ettikten sonra kendine sandalye çekti. Sohbetleri çok uzun sürmedi. Kahverengi kazaklı, masanın diğer ucunda sedyede konuşan mahkûmu dinlerken bolca kafa salladı ve içeri girdi. Saat 12:30’da siyah üniformalı gardiyan avluya girerek sedyedeki kişiyi içeri taşıdı ve avlu kapısı "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1436'}},[_vm._v("kapatıldı.")]),_vm._v(" Kapı kapatıldıktan sonra 5 dakika boyunca hareketlilik görmeyince makarna yemeye karar verdim. Merceğimdeki hareketlilik azaldıkça kendi bedenimdeki hareketliliğe ve açlığıma odaklandığımı düşündüm. Makarnanın içine buzdolabında sakladığım konserve domates sosundan ekledim ve ısıtmaya başladım, yemeğe başlamadan önce salondaki koltukların ve sehpaların yerlerini değiştirmiştim. Birbirine bakan ev mobilyalarının oluşturduğu dairesel düzen yoktu, bir film perdesinin önünde sıralanmış koltuklara benzer paralel olarak yerleşmiş mobilyalar dışarıyı izliyordu. Yeni konumlarında cama dayanmış masamla birlikte manzaram otoyolu, hapishaneyi, uzaktaki tepeleri ve binaları "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0496'}},[_vm._v("bir arada")]),_vm._v(" görüyordu. Saat 13:25, 1. avlunun kapısı tekrar açıldı, ilk çıkan kahverengi kazaklı mahkûm oldu. 50-60 yaşlarında olabilir, ağır adımlarla yürüyor. Arkasından avluya giren bir diğer mahkûm kapıyı kapadı, ikisinin yaşları birbirine yakındı. Yeni gelen mahkûmun üzerinde siyah bir eşofman ve kum renginde pantolon vardı. Kapı kapandığı esnada kahverengi kazaklı avlunun diğer ucuna varmıştı. Gördüğüm kadarıyla, avlular arasında 1. avluya ait bir özellik var. Avlunun merkezinde uzun ince, dikdörtgen şeklinde betonla sarılmış toprak, bu avlunun saksısı olarak bitki yetiştirmeye olanak sağlıyordu. Yürüyüşleri esnasında çiçekleri kontrol eden iki mahkûmdan kahverengi kazaklı, duvara dayanmış pet şişeyi eline aldıktan sonra çiçekleri suladı. Sanırım çiçeklerin bakımını avludaki diğer mahkûmlardan daha sık üstleniyor. Siyah eşofmanlı ise çiçekleri incelerken duraklamadı, "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1405'}},[_vm._v("temposunu sürekli korudu")]),_vm._v(". Bazı anlarda duvara daha fazla yaklaşıp destek alıyor, bazense avlunun merkezine doğru ilerliyordu. Odağımı, o esnada avlular ve ön bahçe arasında gezdirmeye başladım ve gördüğüm kişilere sırayla odaklandım. Bazı zamanlar yüzlere yaklaştığımda beni gördüklerini düşünüyorum. Böyle bir ölçekte görülmek fiziksel anlamda neredeyse imkânsız, hem uzaklık hem de açım nedeniyle. Uzağı netleyen mercekler, uzağa çıplak gözle baktığımda bulanıklaşan gözlerimle oyun oynuyorlar. Bu görsel alışılmamışlık, görülme düşüncesini içime atan ikinci bir oyun olsa gerek. Bu ölçekte yakınlaşmak, odağın içine düşmeye benziyor. Saat 13:42, kahverengi kazaklı kapıya doğru yönelirken kapıda tekerlekli sandalye kullanan biriyle karşılaştı. Avluya çıkan yeni kişi gençti ve arkaya taranmış uzun kumral saçları vardı. Kısa bir diyalogdan sonra, avlunun koğuşa bakan penceresine yönelip içeriden biriyle sohbete başladılar. Konuşma çok uzun sürmedi, kumral olan sigarasını "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0525'}},[_vm._v("konuşma boyunca")]),_vm._v(" en fazla birkaç kere içine çekti. Koğuş tarafında görmediğim şeyler aklımdaki sahneleri çoğaltmıştı. Bunun dışında kalan ve doğrudan bilgi aldığım avluysa gökyüzüne uzayan ve dairemin önündeki boşluğu paylaşan dikdörtgen bir meydan. Saat 13:40, kumral olan çok uzun diyaloglar kurmasa da pencereden koğuşa doğru her turunda sesleniyordu -içeride birden fazla kişiyle konuşuyor olabilir. Avlu pencerelerinden tam girmeyen güneş ışığı içeriyi karartmış ve avlu pencerelerini odağımın merkezinde duran siyah dikdörtgenlere çevirmişti. Buradaki bütün dairelerin camları yarı filmli halleriyle içeriyi saklarken dışarıyı bir ayna gibi yansıtıyor. Uzaktan bakan her kimse, çevredeki manzarayı, parlayan güneşi ve bulutları bu binalar üzerinden görebilir. 1. avluda kahverengi kazaklı mahkûm her zamanki ritminde yürümeye devam ediyordu, bu sefer pencere tırabzanlarına daha yakın, uzun süredir hareket halindeydi, belki de yorulmuştur. O esnada kumral olan gözden kayboldu, diğeri hızını ve temposunu arttırdı. Saat 14:10, 1. avluya çok dikkatimi verdiğimi mekâna uzaklaştığımda anladım, giriş bahçesine taşınmış bir grup karton kutu gördüm, bu aralıkta bir araçla taşınmış olmalı. 1. avluya tekrar baktığımda yürümeye devam eden mahkûma bir kere daha yakınlaştım. Üzerindeki renkleri detaylıca ayırt edebilmiştim, kazağı, kahverengi ve grinin karıştığı bir örgüydü, üzerinde koyu kahverengi kare desenler vardı. Gövdesini duvarın ve avludaki toprağın renklerinden ayıran lacivert bir pantolon giyiyordu. Saçlarının yarısından fazlası beyaz ve her adım atışında sol eli normalden daha hızlı bir biçimde öne doğru düşüyor. İlk odaklandığım zamandaki bilgiler, uzun süreli bir dikkatten sonra, birbirinin üzerine çoğalmıştı. Gördüğüm detaylar kadrajımı doldurdukça, tanıdık olmadığım bir biçimde hafızamda canlandıklarını düşündüm; ne baktığım ne de bakılan noktaya aitler. Saat 15:00, 1. avlu boşaldı, diğer avlularda da kimseyi görmemiştim. 15 dakika sonra 3. avluya bir gardiyan çıkıp hızlıca etrafa baktıktan sonra geri girdi. Güneş, bu saatlerde dürbünüme dik açıyla geldiği için odağımdaki görüntüler "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0561'}},[_vm._v("parlıyor")]),_vm._v(" ve beyaz lekeler oluşturuyordu. Mercek, her şey gibi güneş ışığının gücünü de büyütmüş ve bakmamı zorlaştırmıştı. Parlamalar arasından gördüğüm hareketlilik, ön bahçede, iki görevlinin kısa süreli diyaloğu oldu. Konuşmaları esnasında güneşten kaçmak için gölgeye doğru ilerlediler, bense az görebildiğim dürbünle birlikte güneş tarafından hedeflenmiş ve fark edilmiştim. 15:55’te kimseyi göremeyince sandviç yapmaya karar verdim. 16:17’den başlayarak 1. avluda kişi sayısının arttığını gördüm. Yürüyen sayısı üçtü, bu kişileri önceden avluda hiç görmemiştim. 16:17’de, önce lacivert spor hırkalı ve eşofmanlı biri, sonrasında onu takip eden yaşça daha genç iki kişi girdi. Gençlerden birinin üzerinde kırmızı yelek ve altında kırmızı hırka vardı. Odağımda kalmasının sebebi, avluya çıkan herkesten farklı olarak hızlı ve seri adımlar atıyor oluşuydu. Avlu kapısına kadar gelip diğer uca doğru hızlı bir biçimde dönüyor ve çoğunlukla elleri cebinde dolaşarak avludaki diğer kişilerin diyaloglarına katılmıyordu. Temposunu arttırmaya devam ederken üst katımda oturan kişi camı o kadar şiddetli çarptı ki bir anlığına kadraj ayarlarım bozuldu, bu aslında küçücük bir titreşimdi fakat uzağı bu kadar net görebilen bir aracın durduğu yeri hafif bir rüzgâr bile test edebilir. Titreyen görüntüyle birlikte, sabahtan hatırladığım ayarlarla odağı hızlıca geri netledim. Saat 17:00’da yürüyen 3 kişi avlunun beyaz kapısında, gardiyan ve yanında duran biriyle buluştu, bir bilgilendirme konuşmasına benziyordu. Diyalog sonrası gardiyan elindeki kâğıdı işaret ederek lacivert eşofmanlı kişiye bir şeyler söyledi, sonrasında avludaki herkes içeri girdi ve gardiyan kapıyı kapattı. Aynı saatlerin devamında avlunun dış kısımları hareketlendi, devriye gezilen yolda 3 "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0620'}},[_vm._v("gardiyan")]),_vm._v(" bir araya gelip kısa bir sohbet sonrasında binanın kuzey bölümüne ilerleyip gözden kayboldular. Bu yolun belirli bir kısmını görebildiğim için her devriye elemanının turladığı bir kesite tanıklık ediyorum. Gardiyanlarla şu ana kadar devriye yolu üzerinde, 45-60 dakikalık aralıklarla karşılaştım. Saat 18:00’da 1. avlunun içerisine bakan bir pencerede "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1479'}},[_vm._v("ışık yandı")]),_vm._v(" , hava yavaşça kararıyordu, oturduğum oda hala yeterince aydınlıktı, ışığı yakmayı düşünmedim. 18:00’dan biraz önce 2 kişi bina kapısından çıkış yapmıştı, aralarından biri sabah giriş yapan, kırmızı-siyah çizgili montluydu. Avlu ışıkları da bu saat aralığında yandı fakat büyük spot ışıkları daha yanmaya başlamamıştı. Büyük spot ışıkları 18:15’te yandı. Saat 19:10’da gardiyanlar devriye yolunda tekrar göründü, ikili gezmeye başlamışlardı. Havanın kararması, günün avlular için bittiğinin bir göstergesi olabilir. 17:30’dan sonra bahçede ve devriye yolunda sadece gardiyanlarla karşılaştım. Havanın kararmasının diğer bir etkisi, dikkat önceliğimin değişmesiydi. Akşam odaklandığım bir alan varken karanlıkta kalan diğer alanlara dikkat vermek zorlaşıyor ve ışığın altındaki hareketler daha hızlı dikkatimi çekiyordu. Işık benim için bir alanı aydınlatırken dikkatimi dağıtmakta da çok başarılı, karanlıkta kalmış birkaç kişiyi görmemiş olabilirim. Salonun ışığını hiç açmamıştım, bu süreç sonrasında karanlıkta kalma kararı almıştım, önümdeki otoyol ışıklarından, yolu süsleyen araba farlarından ve cezaevindeki spot ışıklarından içeri yeteri kadar ışık giriyordu. Uzun sakin bir aralıktan sonra saat 20:17’de 4 kişi, ikili gruplar halinde binadan çıkarak bahçe kapısına doğru ilerlemeye başladı, sadece ikisinin elinde poşetler vardı ve kimseyi sabahtan tanıyamamıştım. 3 dakika sonra biri, koşarak çıkış yapan grubun rotasından ilerledi, onlarla aynı taşıta yetişmeye çalışıyor olabilir. Saat 20:30’da iki araç, sürgülü kapının açılmasıyla bina önündeki boşluğa giriş yaptı, biri sağlık aracı diğeriyse askeri servisti. Sağlık aracı acele bir biçimde giriş yapmamıştı, mavi ve kırmızı ışıkları yansa da yol boyunca sirenleri hiç ses çıkarmadı. Binanın, araç giriş kapısına tam paralel konumlanan geniş metal kapısı vardı. Bu kapının ölçeği ve büyüklüğü nedeniyle "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0713'}},[_vm._v("nakil")]),_vm._v(" işlemleri için kullanılan bir alana bağlı olduğunu düşündüm. Kapının gövdesinde tekrar eden katlar, her katlanmada kuvvetlenen maddeler gibi bu büyük metal parçayı ayakta tutuyordu. Giriş yapan sağlık aracından 2 gardiyanın eşlik ettiği, elinde seruma benzer bir torba taşıyan yaşlı biri çıktı. Askeri servis aracından yaklaşık 5 gardiyan çıktı ve yanlarında bir kişi daha getirdiler. Siyah saçlı ve genç biri olduğunu az da olsa karanlıktan anlayabilmiştim. Açık duran kapıdan içeri doğru odaklandığımda taşıma için sedyelerin ve tekerlekli sandalyelerin bekletildiğini görebiliyordum, onlara bu durumda ihtiyaç duyulmamıştı. İçeri nakledilme işlemi tamamlandıktan sonra gardiyanlar bir araya gelerek konuşmaya ve sigara içmeye başladılar. Mavi üniformalılar çoğunluktaydı, siyah üniformalı iki kişi nakliye esnasında ön kapıdan çıkıp gruba katılmıştı. Bazı yüzleri, far ışıkları nedeniyle sabah devriyesinden tanıdığımı düşündüm, devriye süresince daha genç olarak tanımladığım 2 gardiyan ordaydı, aynı zamanda köpeği izleyen kişilerden biri de aynı grubun içindeydi. Toplanma 20:53’te, herkesin servislere ve görev yerlerine da