u içeri giriş yaptı, geri geri gelerek arka kısmını nakliyenin yapılacağı alana doğru çevirdi. Araç, nakliye alanına çok yaklaştığı için bina ile arasında neredeyse boşluk yoktu, ne taşındığını göremedim, devriye gardiyanı sayısı ise 3’ten 2’ye düşmüştü, belki biri nakliyeye yardım etmeye gitmiştir. Kamyonetin arka kısmını göremiyordum, dikkatimi aracın ön camındaki yansıma çekti. Yansımadan, oturduğum binanın dış hatları ve pencereleri gözüküyordu. Bina, aracın camındaki eğim yüzünden bozulup bükülmüş bir halde, fiziksel ve yapısal özelliklerinden uzaktı. Oturduğum sitenin gerçekliğinin dışında kalan hayali konutlara benziyordu ve ben de eğilip bükülmüş pencerelerin herhangi birindeydim. Bana rüyamı hatırlatmıştı. Yansımayı izlemeye kısa süreliğine ara verip banyodan diş fırçamı aldım. "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0875'}},[_vm._v("Araç")]),_vm._v(" 11:27’de çıkış yaptı. Yaklaşık 10 dakika boyunca dişimi fırçalamıştım. 12:15’te iki servis aracı içeri girdi. Araçların birinden çıkan siyah üniformalı iki görevli binaya girdi. Aynı zaman diliminde bir ziyaretçi ön kapıdan içeri girdi. Giriş kapısının yanında duran kulübedeki uzun diyaloğu ve elinde tuttuğu kağıtları görünce ziyaretçi olduğunu düşündüm. Siyah tişört ve kot pantolon giyiyordu, montu elindeydi ve diğer elinde tuttuğu kâğıtlarla hızlıca "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0900'}},[_vm._v("içeri")]),_vm._v(" girdi. Açık camımdan giren rüzgârdan havanın soğukluğunun arttığını düşündüm. 12:36, yaklaşık 20 dakikalık aralıkta ziyaretçi sayısı arttı, giriş bahçesinde vakit geçiren 4 tane ziyaretçi vardı. İki kişi birlikte gelmiş, ön bahçede turlarken sohbet ediyorlardı. Mercekle yakınlaştığım ve ziyaretçileri odağıma aldığım anlarda, göz evrenimde herkesin zeminini oluşturan "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0928'}},[_vm._v("gri beton")]),_vm._v(" tekrar ediyor; avludaki mahkûmu, devriyedeki gardiyanı ve bahçedeki ziyaretçileri aynı doku üzerinde görüyorum. Bu kadar yakından bakıldığında çevreye dair bilgi, odağın merkezinden 3-4 metre dışarı uzayan çemberin sınırlarına ait. Mercekle odağa yaklaşırken ve çerçevem gittikçe daralırken gördüğüm her ne ise avluyu, bahçeyi ve devriye yolunu birbirinden ayırıyor. 13:15’te ambulans içeri girdi ve gardiyanlarla birlikte gri kazaklı genç mahkûm içeri götürüldü. Diğer bütün nakliyelere göre çok hızlı bitmiş ve ambulans gözden kaybolmuştu. Saat 14:00, daha geniş bir alanı görebileceğimi düşünerek terasa gitmeye karar verdim. Öncesinde eşyalarımı toparlayıp taşınabilir hale getirdim. Terasın açısının nasıl olduğundan ve birilerinin olup olmadığından emin olmak için yanıma sadece fotoğraf makinesini aldım. Terasa ulaştığımda kullanılmayan bir alan olduğunu düşündüm; merkezinden geçen havalandırma kanalı ve onun üzerinden yukarı doğru uzayan küçük ölçekli bir vinç, teras alanını iyice daraltmıştı. İnşaatlarda kullanılan vinçlere benzeyen bu iskeletler çoğunlukla boylu boyunca binayı saran cam alanların temizlenmesi ve bakımlarının yapılması için metal kafesleri taşımakta kullanılıyor. Vincin terastaki varlığı ise buraya, makine dairesinin özel ve görevlilere ait tenhalığını sağlamıştı. Ortak alan olsa da burada geçireceğim süre için iyi hissetmiştim. Daireye dönüp dürbünü yanıma aldım. Teras, oturduğum bloktan daha doğuya baktığı için cezaevinin ilk 8 avlusunu birbirine yakın açılardan görebiliyordum. Dürbünü kurduktan yaklaşık 10 dakika sonra, 2. avlunun köşesine bir grup toplanmış, sohbet etmeye başlamışlardı. Sohbete dürbünle dahil olup dudak okumayı denedim ama kelimeleri ayrıştıramadım. Doğuya dönük cephe bu saat aralığında güneşi tam karşıma getirmiş ve terası boydan boya aydınlatmıştı. Güneşin doğrudan merceğe vuracağını tahmin etmemiştim. Odaklandığım alanların her biri ışığı üzerine çekip parlatırken bazen bulanıklaşıyordu. Bir süre kayıt almaya devam ederken, dürbünden uzaklaşıp çevreyi izlemek için terasın korkuluklarına yaslandım. Burası sıkça rüzgâr alıyor, güneş olmadığı zamanlarda gittikçe soğuyacağı kesin. Telefonumdan hava durumunu incelediğimde bulutlu günlerin başlayacağını gördüm. Gün boyunca terasta olacağım için buradaki kurulumu düşünmeye başladım. 14:00’da "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0952'}},[_vm._v("ikinci avludaki")]),_vm._v(" sohbete tekrar yakınlaştım. Grupta, diğerlerine kıyasla iri olan en kenarda oturmuştu, çoğunlukla o konuşuyordu ve masanın etrafına dizilmiş diğer 4 kişi onu dinliyordu. Uzun soluklu bir konuşmanın ardından diğerleri de dahil oldular. İri olan, ilk gün gördüğüm uzun siyah saçlı kişiyle yüz yüze gelerek bire bir konuşmaya başladı. Sohbet uzadıkça uzun saçlının sıkılmaya başladığı anlaşılıyordu. 1-2 dakika sonra sandalyeden yavaşça kalkarak avluda turlamaya başladı. Masada kalan biri yaşlı diğeri genç iki kişiyi önceki günlerden hatırlamıyorum. Genç olan, desenli koyu renk bir kazak, yaşlı olan benzer renklerde desensiz bir kazak giyiyordu. Uzun saçlı olan, avludaki turu esnasında köşede oturan iri mahkûma uzanarak bazı şeyler söyledi ve avlunun kapısından içeri girdi. Masada kalan 3 kişi konuşmaya devam etti. Yaşça genç olan sadece dinliyor, masanın en yaşlısı aralıklarla konuşarak sohbeti sürdürüyordu. Bir anlığına apartman boşluğundan gelen bir ses duydum, arkamı dönerek terasın kapısına doğru sesi kontrol etmeye gittiğimde apartman boşluğunda çalışan temizlik görevlisiyle karşılaştım. Terasa girmemişti ve ne yaptığımı sorduğunda, burada oturduğumu, sitenin etrafını fotoğrafladığımı ve ortak teraslar kilitli olduğu için yukarıyı tercih ettiğimi söyledim. O da bana, önceden geniş terasları açtıklarından fakat apartman sahiplerinin hayvanlarının tuvalet ihtiyaçları için bu alanları kullandıklarından bahsetti, bu nedenle terasların kapıları vidalanıp sabitlenmişti. Böylece terasları incelerken gördüğüm vidaların sebebini öğrenmiş oldum. Diyaloğumuz 1 dakika bile sürmemişti, görevli terastaki halimden kaygılanmayıp alt kata indi, bense terasa geri çıktım. Avlularda bir anlığına hareketlilik artmıştı, öğle saatlerinde verilen bir paydosa benziyordu. 7. avluda iki kişi yan yana, avlu boyunca yürüyorlardı, ikisi de siyah kazak ve kot pantolon giymişti, "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1282'}},[_vm._v("siyah ayakkabıları")]),_vm._v(" ise birbirininkine benziyordu. Aynı tempoda yürüyerek sohbet ettiler, yakın arkadaş olabilirler. 5. avluda, masanın etrafında 3 kişi çay içerek aralarından birinin elinde tuttuğu nesne hakkında konuşuyorlardı, ne olduğunu tam anlayamadım. Koyu renkli olduğu için hatları belli değildi, ben de anlayabilmek için onlarla birlikte gözümü kısmıştım. 3. avluda, üzerinde çiçekli minderler bulunan iki boş sandalye duruyordu. Çevresinde hareketlilik yoktu, sırt sırta hizalanmışlardı. Saat 15:00’dan önce avlulardaki yoğunluğun artmasının izin saatleriyle ilgili olduğunu düşünüyorum. 15:00’dan sonra yoğunluk azaldı. Acıkmıştım, yemek yemek için daireye dönmeye karar verdim. Daire kapısını açmaya çalışırken anahtarlık kapının kolundaki aralığa sıkışarak zincirinden koptu. Anahtarlığın bir ucunun şeklinin değiştiğini gördüğümde elimle bastırarak düzeltmeye çalıştım. Saat 15:20, anahtarlığın şeklini eski haline getirdikten sonra buzdolabından aldığım hazır pizzayı ısıtıp hemen yedim ve hızlıca terasa döndüm. Terasa yerleşmeye başlamamla birlikte bir askeri araç içeri giriş yaptı, içinden şoför ve üniformalı biri çıktı, bahçede bir süre dolaşıp içeri girdiler. Saat 15:50’de iki ziyaretçi içeri girdi -genellikle ziyaretçilerin kıyafetleri siyah renkte oluyor. Saat 17:00, içeri giren ziyaretçilerden biri 2-3 dakika sonra dışarı çıktı. Ziyaretçilerin içeride geçirdikleri süre değişebiliyor. 17:15’te devriye gezen gardiyan sayısı 1’e düştü. Devriye saatlerine alışmıştım. Bu kısmı akşam saat 23:00’da yazmaya başlıyorum. Terasta olduğum sürede daha çok ses kaydı aldım. Güneş ben terastayken 17:30 civarında batmaya başlamış ve hava yavaşça kararmıştı. Güneş battıktan sonraki 1 saat boyunca hareketlilik olmamış, ben de havanın soğukluğu nedeniyle belirli sürelerde terasın apartman boşluğuna bağlanan aralığına girip, ısınıp, tekrar terasa çıkıyordum. Avlularda hareketlenme görmezken 3. avluda bir anlığına 3 kişi gördüm, biri elinde top tutuyordu. Sarı topun etrafındaki 3 kişinin turuncu, siyah ve kahverengi kıyafetleri karanlıkla birlikte avluda gezen hacimlere dönüşmüştü. Top oynadıkları süre boyunca avludaki hareketler bu hacimleri birbirine çarpıştırıyor ve kesiştiriyordu. "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1014'}},[_vm._v("Turuncu tişörtlü")]),_vm._v(" topu diğerlerinden daha çok kontrol ediyor, aynı zamanda diğerleri dinlenirken topu, duvar ve kendi arasında sektirerek oyunu sürdürüyordu. Avlunun bir duvarından diğer duvarına uzanan ipte asılı olan "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1081'}},[_vm._v("giysiler")]),_vm._v(" kalenin sınırlarını belirlemişti. 3 kişi oynadıkları için kahverengi tişörtlü, belirli aralıklarla takım değiştirerek oyunu eşitliyordu. Oyun başladıktan 20 dakika sonra koğuş kapısı açıldı ve temizlik görevlisi avludaki çöpleri alırken siyah üniformalı gardiyan avluya girdi. Avludakiler top oynamayı bırakıp toparlanmaya başladılar. Herkes koğuşlara girdikten sonra gardiyan avluda son bir tur atmaya başladı. Bir duvara yaklaşarak kısa süreliğine yakından inceledi ve ardından koğuş kapısını kapayarak içeri girdi. Açık alanlar tekrar sakinleşmişti, bina boyunca sıralanan pencerelerin açılıp kapanan ışıklarını takip ediyordum, bazen açık ışıklarda gölgeler kısa süreliğine görünüyor ve kayboluyordu. Terasta açık havada olmak geceye doğru beni canlandırmıştı. Gün boyunca sadece bir görevliyle karşılaşmam, buranın tenhalığını destekliyordu. "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1005'}},[_vm._v("Saat")]),_vm._v(" 22:00’a kadar terasta zaman geçirip dürbünle Ay’a yaklaştım, az da olsa kraterlerin girinti ve çıkıntılarını bu orta-uzaklık dürbününden görebiliyorum. Terasın geç saatlerde açık olduğundan emin olmak için bir süre daha terasta kaldım. Avlularda kimse yoktu. Devriye yapan kişi sayısı ikiydi. Bu akşam, diğer akşamlardan farklı olarak, nakliye için hiç araç gelmedi. Daireye dönene kadar dürbünle gökyüzünü izlemeye devam ettim. 22:45’te daireye döndüm, hazır pizzanın kalanını yedim. Apartmanın pencerelerini kapalı bıraktığım için daireyi boğucu bir sıcaklık basmıştı. Merkezi ısıtma sistemli bu dairelerin ideal ısısı benim için yüksek. Önceki günlerde camları açarak vakit geçiriyordum. Tekrar daireden hapishaneye doğru baktım; aradaki boşluğu ses ve grafik olarak dolduran otoyol, trafikle birlikte ışıldamıştı. Dairemin ve avluların durağanlığı ve sessizliği arasında dolaşan, ritmi ve hızı nedeniyle sıkça bulanıklaşan bir yoldu. Bu binada ve hapishanede, bizi çevreleyen "),_c('inline-image-link',{attrs:{"image-i-d":'MOV_1006'}},[_vm._v("ritmin")]),_vm._v(" dışında, benzer zamanlarda yatıp kalkıyor ve bazen açık havaya çıkıyorduk. Saat 4:00, son notumdan sonra 1 saat boyunca egzersiz yaptım. İzlerken hareketsiz kalan vücudumun hamlığını gün boyunca hissetmiştim. Gece boyunca gardiyanlar dışında kimseyi görmedim. Devriye her zaman, 45-50 dakika aralıklarla benzer yerde oluyor. Dinlenip çevreyi izlerken, 1 saatlik uykunun enerjimi toplamak için yeterli olduğunu düşünüyorum. ")],1),_c('div',{attrs:{"id":"section-3"}}),_c('AmpersandSeparator'),_c('span',[_vm._v(" Sabah 5:00’dan 6:00’a kadar süren kısa bir uykuya daldım. Kalktığımda çevrem sessizdi, mutfaktan bal ve tereyağı aldım ve camın kenarına geçtim. Neredeyse her sabah bal yerim, burada kaldığım süreçte şimdi yeme fırsatım oluyor. Hava, dün incelediğim tahminlerle uyuşuyordu. Erkenden teras planları kurarken, bulutlu havanın gün boyunca devam etmesi, güneşin mercek üzerinde parlaklık yapmayacağına işaretti. Dinçliğimin sebebi uyuduğum saat aralığıyla mı ilgili, bilmiyorum. Sürekli ve düzenli çalışarak bal üreten arıların motivasyonunun kısa süreli yanıltıcı etkisi de olabilir. 9:05’te terastaydım, dürbünün ayaklarını kurmaya başlarken 3 araç içeri girdi. Askeri araç, servis aracı ve ambulans bir aradaydı. Girişe en yakın mavi askeri aracın içerisinden siyah üniformalı bir gardiyan çıktı, onu karşılamak için binadan çıkan gardiyan aynı üniformayı giyiyordu. Nakil sürecine eşlik etmek için mavi üniforma giyen gardiyanların sayısı çoğalmaya başladı. Üniformaların renklerini belirli görevler üzerinden ayrıştırmaya başlamıştım. Siyah üniformalılar, iç mekândaki kontrolü sağlayan ve mavi üniformalılardan sorumlu olan gardiyan grubuydu, sayıları azdı. Genellikle nakliye süresince emirleri onlar veriyor, mavi üniformalılarsa onları takip ediyorlardı. Arabaların çevresinde toplanan gardiyanlar 9:15’e kadar aralarında konuştular. İki farklı renkteki üniformalar iç içe geçmiş ve gardiyanları ayırt etmemi zorlaştırmıştı. Saat 9:20’de 4 gardiyan, içeriden siyah hırkalı ve yeşil pantolonlu biriyle dışarı çıktılar, mahkûmun ellerindeki kelepçe çevresindeki kalabalık içinden, merceğimde parlamıştı, yaşı 25 civarında olabilir. Genellikle mahkûmlar askeri araca bindirilirken bu sefer mahkûm, "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1237'}},[_vm._v("beyaz")]),_vm._v(" renkteki servis aracına bindirildi. Servisin camları filmlenmiş ve üstü ince metal tellerle örülmüştü, içeriyi ancak açılan arka kapısından görebildim. Ambulans ve askeri araca başka kimse taşınmadı, mavi üniformalı gardiyanlar boşalan iki araca bindikten sonra araçlar çıkış yaptı. Bu araçların bazıları, cezaevi içerisinde gardiyanların ulaşımını sağlamak için kullanılıyor. Saat 9:45 olduğunda bir kamyonet girişe doğru yaklaştı, normalde bu saatlerde çöp aracı bekliyordum, içeri giren beyaz kamyonete önceki günlerde rastlamamıştım. Araç, nakliye alanına çok yakınlaşsa da dar bir aralıktan nakil işlemlerinin yapıldığı kapıyı görebiliyordum. Aralığa odağımı sabitledim ve bekledim, gölgede kalan bu aralıktan turkuaz rengi, çevresini saran dokulara kontrast bir biçimde ortaya çıkmıştı. Bir insanın boyuna eşdeğer turkuaz renginde küplerdi, aralığa doğru yakınlaşarak merceği netleştirdiğimde bu hacmi, plastik su şişelerinin oluşturduğunu fark ettim. Üst üste ve yan yana sıkı sıkıya hizalanmış bu pet şişeler birbirlerine sarılmış taşınıyordu. O kadar fazla sayıda paket taşınıyordu ki cezaevinin ölçeğini ve içerideki kişilerin sayısını bir anlığına hayal ettim. Bu paketler, aklımdan çıkmış bir ölçeği bana basitçe "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1213'}},[_vm._v("sayılar ve boyutlar")]),_vm._v(" üzerinden geri hatırlatmıştı. Taşıma işlemi boyunca her bir şişenin içerisindeki su değişen dengeye aynı anda tepki veriyor ve paketin içinde oluşan kocaman bir dalgaya dönüşüyordu. Uzun bir süre boyunca taşıma işlemini izledim. Dikkatim hala paketlerdeyken sağanak yağmur başladı, odağım ve benim aramda yağmur tarafından hafifçe filtrelenen buğulu ve bulanık bir doku oluştu. Bu doku beni bir yanıyla rahatlattı, daha az görünür olmamla ilişkili bir rahatlamaydı. Sıklaşan yağmur yüzünden, terasta bulduğum bir poşetle dürbünü sarmaya başladım. Poşeti geçirirken ayaklarımın altından ilerleyen su birikintisi terasın giderine doğru yöneldi. Dürbünden baktığımda avlulardaki su birikintilerinin arttığını gördüm. Biriken sudan yansıyan gökyüzü manzarası avlunun merkezinde gittikçe büyümeye devam etti. Su o kadar hızlı yayılmıştı ki kadrajımı dolduran beton avluyu yansımadan göremiyordum. Poşeti bu süreçte merceğin çevresine iyice dolamıştım. Giriş kapısına odağımı çevirdiğimde su kolilerini getiren beyaz kamyonet yoktu, beklediği yerde yağmurdan "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1225'}},[_vm._v("koruduğu")]),_vm._v(" kuru zemini gördüm. Saat 10:25’te, çöp aracı her zamanki saatine göre yaklaşık 1 saat gecikti. Çöplerin hazırlanıp dışarıya sıralanmasında benzer bir gecikme oldu. Hızlıca yüklenen çöplerden sonra araç çıkış yaptı. Mekânın ritmine dair aksaklıklar takip sürecim uzadıkça daha hızlı dikkatimi çekmeye başlıyor. Bu aralıkta avlularda kimseyi görmemiştim, dikkatimi tekrar araç girişine verdim. 10:45’te, erken saatlerde gördüğüm servis aracı yeniden giriş yaptı. İçerisinden inen üç kişiden en genci silahını iki eliyle tutarak minibüsün arka kapısının yanında beklemeye başladı. Silahı o şekilde tutuşuyla ilgili olsa gerek, diğeri kaşlarını çatarak bir süre genç gardiyanla konuşurken elini işaret etti. Konuşan kişinin üst rütbeli olduğunu kolundaki grafiklerin fazlalığı üzerinden tahmin etmiştim. Genç gardiyan elindeki silahı beline doğru sarkıttı ve geride kalan gardiyanın araçtan çıkmasıyla birlikte binaya doğru ilerlediler. Sürekli hedefe doğru doğrultulan, görüntüyü takip etmek ve netleyebilmek için dürbünü tutan ellerimin konumu, genç gardiyanın uyarıldığı tutma şekline benzerdi; dürbünün arkasında kalan elim kadrajı ve netliği ayarlarken, öndeki elim görüntüdeki titremeleri azaltmak için "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1245'}},[_vm._v("merceği kavramıştı")]),_vm._v(" Servis aracı gardiyanları bıraktıktan sonra çıkış yaptı. 10:55’te iki ziyaretçi gördüm, ellerindeki dosyalarla hızlıca giriş kapısına doğru ilerlediler, şu ana kadar gördüğüm ziyaretçiler arasında en genç iki kişi olabilir. Dış kapı ile iç kapı arasındaki anlarını yakalamak için hareketlerini tahmin ederek kadrajımı sabitledim. Devriyenin, nakliyenin ve ziyaretçilerin sirkülasyonlarında sıkça kullandıkları kestirmeleri ve rotaları bilmek, kadrajı hazırlamamı ve durumu kaydetmemi kolaylaştırmıştı. Başlarda oynadığım tahmin oyunu zamanla, önceden kadrajımı hazırladığım bir plana dönüşmüştü. Bu rotalara çok güvenirsem birilerini "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1266'}},[_vm._v("odağımdan")]),_vm._v(" kolayca kaçırabilirim. Saat 11:10, yağmur dindi ve avlu kapıları açılmaya başladı. 8. avludaki 2 arkadaş her zamanki ritimlerinde sohbet ederek turlamaya başlamıştı, bu sefer onlara dahil olan 3. mahkûm 5 dakika sonra ritimlerine ayak uydurdu. 3. avluda ise, akşamüstü top oynayanlar arasından turuncu tişörtlü, zemindeki su birikintilerini avlunun giderine doğru süpürüyordu. Terastaki giderlerin topladığı su, ayağımın altında koyu gri izler oluşturmuştu. Plastik sandalyeler bu süreçte dışarı çıkarıldı, o an sadece avlunun değil, koğuşun içinin de temizlendiğini anladım. Koğuştan avluya doğru itilen köpüklü sular giderlerin çevresinde toplandı. Bembeyaz köpük, avlunun dışına doğru yol aldıkça iyice koyulaşıyor ve gidere ulaştığında koyu gri renkte bir bulut halinde yok oluyordu, biraz önceki sağanak yağmura hazırlanan bulutlardan farksızdı. Dün top oynayan diğer iki mahkûm, temizliğin devamında süpürgeleri devraldıktan sonra koğuşun içinden plastik sandalyeleri çıkarmaya başladılar. Avlunun merkezine tek bir çizgi halinde sıralanan sandalyeler birbirinin arkasına yaslanarak uzamaya devam etti. Üzerlerinde taşıdıkları minderler, karton parçaları ve çizikler üzerinden bazılarını "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1345'}},[_vm._v("hatırladığımı")]),_vm._v(" düşündüm. 11:30’da 6. avlu dikkatimi çekti. Odağımı sabitlediğimde bu avlunun diğerlerinden bütünüyle farklı olduğunu hatırladım. Burada kaldığım süre boyunca dikkatim parçalar halinde bir odaktan diğerine bağlandığı için hareketsiz alanları unutmuştum. Bu avlunun arka duvarına paralel olarak inşa edilmiş taştan bir yapı, yapının çevresinde konumlanmış birkaç saksı ve yan çevrilmiş bir masa vardı. Neredeyse avlunun merkezinde duran "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0968'}},[_vm._v("bankla")]),_vm._v(" birlikte diğer manzaram olan, otoyol ve metronun çevresindeki herhangi bir kesite yakınlaşmıştım. Duraklarda bu kesitin etrafını saran yayaların yerini avluda kenara itilmiş eşyalar almıştı. Kocaman, açık hava bir depoya benziyordu. Bu avluya dikkatimi vermemin ilk sebebi, içeri giren kot pantolon ve mont giymiş biriydi. Kat kat giyindiği üzerindeki şişkinlikten anlaşılabiliyordu. Yürüdüğü süre boyunca avlunun iki ucu arasında çizgisel olarak dolaşmadı, avlunun merkezinde daire çizerek duvarlara çok yaklaşmadan turlarına devam etti. Avluya çıktığı süre boyunca elleri cebindeydi, benim ellerimse soğuyan havayla birlikte odağımı titretiyor. Saat 11:55’te, avlulardaki kişi sayısı azalırken, "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1306'}},[_vm._v("1. avluda")]),_vm._v(" itminden şaşmayan kahverengi kazaklı mahkûm içeri girmeden önce son turlarını atıyor. İçeri girdikten hemen sonra elinde 5 litrelik su şişeleri ile avluya geri döndü. Elindeki şişeleri avlunun ortasındaki beton saksılığa yasladıktan sonra içeriden 4 tane daha su getirerek her bir şişeyi yan yana dizdi. Havanın soğukluğunu kullandığı bir yöntemdi -ılık suyu hiç sevmem. Şişeleri taşıdıktan sonra avlunun kenarında duran sandalyeye oturup dinlenmeye başladı, dinlenirken avluya gelen siyah hırkalı ve bereli mahkûmla kısa süreliğine sohbet ettikten sonra ikisi birlikte içeri girdiler. Yaklaşık 20 dakikadır kimseyi görmüyorum, sert rüzgârı hissetmeye daha çok başladım. Belirli aralıklarla terasın apartman boşluğuna bağlanan kapalı kısmına girerek ısınıyorum. Sakinlik 30 dakika daha devam etti. Terastaki kesintisiz sürecim nedeniyle dairedeki tuvalete gitmeyi düşünmedim, terasta çok görünmeyen bir köşeye işedim. Aklıma bir önceki gün görevlinin, terasların kapatılma nedeninden bahsetmesi geldi. Temizlik görevlisi “İnsanlar, hayvanlarını dışarı çıkarmayıp tuvaletlerini terasa yaptırdıkları için kilitlendi” demişti. Hatırladığımda gülümsedim. Saat 13:05’te avlularda birkaç kişi görünmeye başladı, 1 saatlik aranın yemekle ilişkili olduğunu düşündüm. İlk hareketlilik çok hızlı bir biçimde gözüme takıldı. 8. avluda biri koşuyordu. Ritmi şu ana kadar turlayan herkesten hızlıydı, nefes alışverişi, düzenli ve uzun vadeli bir koşu olacağını düşündürüyordu. 10 dakika boyunca aralıksız turlamasına yakınlaştım, duvarın sonuna kadar gidip seri bir biçimde dönerek turunu tamamlıyor ve ellerini göğüs hizasından indirmeden temposunu gittikçe hızlandırıyordu. Aklıma, akşamüstü duvarla kendi arasında sürekli top sektiren mahkûm geldi. İkisi de vücutlarını kuvvetlendiren ve dengeleyen bir "),_c('inline-image-link',{attrs:{"image-i-d":'MOV_1326'}},[_vm._v("tekrar")]),_vm._v(" içinde, dışarıdaki bilinmeyene dair koşulsuz çalışıyorlardı. Toplamda 20 dakika süren koşu sonrasında koşucunun yorularak nefes nefese kaldığını ağzından çıkan dumanın sıklığından fark etmiştim. Koşuyu bitirdikten hemen sonra soğuk havada kalmamak için hızlıca içeri girdi. Saat 13:25’te 5. avluda, sabah masaya bırakılan bardaklar 2 kişi tarafından toplandı ve içleri dolu bir biçimde geri getirildi. Sonrasında masaya 2 kişi daha katıldı, aralarında yaş farkı olduğu kolayca anlaşılıyordu. Sandalyeleri masaya doğru çeken gençler diğerleriyle birlikte oturarak sohbet etmeye başladı. Önceden masada oturan iki kişinin başında çizgili bere vardı, kat kat giyinmişlerdi, gençlerin üzerinde ise tişört ve eşofman vardı. Kısa bir sohbet sonrasında çizgili bere giyen iki mahkûm yürümeye başladılar, masada kalanlarsa sohbete devam etti. Sohbetleri devam ederken 2. avluda 2 kişinin yan yana yürüdüğünü gördüm, yakınlaşmaya başladığımda yürüyenlerden birinin başında bandaj olduğunu ve yanındaki mahkûmdan destek aldığını fark ettim. Aynı ritimde avluyu turlarken konuşuyorlardı, yaşları birbirine yakındı. Her duvara yaklaştıklarında bandajlı mahkûm kendi etrafında dönüyor ve destek almak için kollarını yanındakine kenetliyor. Rüzgâr artmaya başladı, dürbünüm olduğundan daha fazla sallanıyordu, bu esnada avlulardakiler çalkalanan odağım içinde koğuşlara yönelmeye başladı. Uzun bir süre boyunca bu rüzgâr sürerse zamanla midem bulanabilir. 3. avluda dışarıda bırakılan tek sandalye rüzgâr nedeniyle düşüp sürüklendi. "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1380'}},[_vm._v("Düşen sandalye")]),_vm._v(" 13:40 civarında avlulardaki son hareketti. Avlu sakinliğinin devam edeceğini tahmin ediyordum. Saat 14:00-16:00 arası genellikle avlular boş oluyor. 40 dakika sonra acıkmaya başladım. Bir parça hazır pizzayı tezgâhta bırakmıştım. Hızlıca daireye dö