ı ve dikkatimi avlulara verdim. Önceki günün kalabalığına göre etraf tenhaydı, 2. avlunun kenarında sadece iki kişi oturuyordu, masa sohbetlerinde genellikle en konuşkan olanla onun yanında oturan bıyıklı uzun saçlı arkadaşı sohbet ediyorlardı. Masa bu sefer avlunun bir ucundan diğer ucuna çekilmişti -dün avlunun köşesi güneş aldığı için yerini değiştirmiş olabilirler. Bulundukları yer, yaslandıkları duvarın koğuş kapısına en uzak yeriydi. Onların sohbeti sürerken, bir anda dikkatimi uzaktaki gözcü kulesinin açılan kapısı çekti, kapı bir süre boyunca açık kaldıktan sonra mavi üniformalı "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1921'}},[_vm._v("gardiyan")]),_vm._v(" dışarı çıkarak kule ile duvarın kesiştiği yere doğru yürüdü -gözetleme kuleleri ile tuvaletin arasında çok mesafe olabilir- gözetleme kulesinin altıdaki köşeye işemeye başladı, sonrasında kapıyı kapatarak kuledeki yerine döndü. Saat 15:10, 3. avluda yeniden sandalyeler tek bir alanda toplanmış. Bu sefer az sayıda sandalye dairesel biçimde yerleştirilmişti, bazılarının üstünde kıyafetler vardı. Temizlik yapılmıyordu, en azından benim gördüğüm alanda yapılmıyordu. Sandalyelerin etrafında turlayan mahkûm ise önceki günlerden top oynarken hatırladığım koyu yeşil yelekli mahkûmdu. Avluda dolaşırken sıkça yukarı doğru bakıyordu, onun kadrajına büyük ihtimalle sadece benim bulunduğum rezidanslar ve uzaktaki çok uzun iş merkezleri giriyor. Sadece gökyüzü manzaralı avlu için dışarısının en güncel bilgisi bu rezidansın yansıtıcı camları olabilir. Duvar yüksekliğinin sınırladığı bilgi alanı benim bulunduğum bina olurken aynı şekilde benimde bilgi alabildiğim alan burasıydı. Sürekli gökyüzüne gözünü dikmiş birinin fikri aklıma astronotları getirmişti. Tabii ki yukarı bakan her kişinin beni görüp göremediğini kendime sıkça soruyordum fakat koca bir dokudaki küçücük, hareketsiz bir noktayı bulmak güçlü ve sürekli bir dikkat gerektirir. Saat 15:45, servis aracı 4. sefer giriş yaptı. Bugün bu aracın sirkülasyon