n elinden geleni yapıyor. Saat 18:50, iki çalışan elinde poşetlerle ana kapıdan çıkış yaptılar, poşetlerin şeffaflığından dolayı içlerindeki üniformaların yeşil renkte oldukları görünüyordu. Çalışanlar çıkış kapısından geçerken devriye gardiyanlarıyla kesiştiler, önceden gardiyanlar arasında görmediğim iri biri bu sefer iki kişilik gruba eklenmişti. Kısa süren kalabalık, kendini uzun bir sakinliğe bıraktı, ben de otoyoldan gelen korna seslerinden sonra dikkatimi yine yolun çevresine çevirdim. İnşaat alanlarından birinin merkezinde duran "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2045'}},[_vm._v("sandalye")]),_vm._v(" rüzgâr nedeniyle düşmüştü, 3. avluda düşen plastik beyaz sandalyenin aynısıydı. Çevreyi saran inşaat malzemelerinin etrafında dolaşan güvenlik düşen sandalyeyi fark etmedi. Ağır bir biçimde inşaat malzemelerini inceledikten sonra güvenlik kulübesine geri döndü. 19:35, hava dün akşamki kadar soğuk olmadığı için terasta uzun süre kalmak beni üşütmemişti, yanıma aldığım atıştırmalıklarsa beni tok tutmuştu. Terasın korkuluklarının arkasındaki geniş alana oturup aşağı doğru baktım. Hava artık tamamen karardığından görünmek gibi bir kaygım yoktu, devriye yolları gibi benim de üzerime çatının gölgesi düşüyordu. Oturduğum süre boyunca görüş açım her uca uzamış, hapishanenin arka sınırlarını ve otoyolun kuzey ucunu görebiliyorum. En uzaktaki avlular çıplak gözle boyutsuz grafiklere benziyorlardı. Her avlunun birleştiği duvarların üzerindeki tellere takılmış nesneler gördüm. Gözüm o nesneleri sadece çeşitli hacimler olarak görürken, terasın içine doğru atlayıp dürbünümü terasın kenarına yasladım ve odağımı ayarladım. Yakınlaşabildiğim kadarıyla, poşete sarılmış birtakım biçimler görebilmiştim, sıkıca sarılmış poşetlerin mat olması içindekileri tahmin etmemi zorlaştırmıştı, poşetlerin dışında tellerde asılı kalmış toplar görüyordum. Avlular arasındaki iletişim biçimlerinden birinin, sarılı not fırlatmak olduğunu daha önce bir yerden okumuştum, belki de bunlar başarısız denemelerle sonuçlanan bir seri nottu. Devam eden sakinlik üzerine eşyalarımı toplayıp "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0979'}},[_vm._v("daireye doğru")]),_vm._v(" yola çıktım. Saat 20:37, araç ve misafir girişine bağlı olan gardiyan kulübesinin penceresinde ışıklar her zamankinden fazla aydınlandı. Normalde kulübedeki gardiyan giriş-çıkışları, araç kapısını ve evrakları kontrol etmekle görevliydi. Sadece bu akşama özel o kulübenin ışığı gözüme fazlasıyla çarpmıştı. Aramızdaki tel örgülerin sıklığından dolayı kulübeyi detaylı görmem genellikle zor olurdu, bu sefer gardiyanın yemek yediğini anlayabilmiştim. Bir süre pencere aynı aydınlıkla kalmaya devam etti, 15 dakika sonra eski haline döndü. Kadrajımı pencerede sabitleyip yemek için buzdolabına gittim, buzdolabında dünden kalmış "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1152'}},[_vm._v("makarna")]),_vm._v(" vardı. Yemeği yemeye başladığımda saat 21:05 olmuştu, devriyeyi görmem yakın. Odağımı giriş kulübesinde sabitlediğim için kısa süreliğine devriye gardiyanlarının pencereye uğrayıp sohbet ettiklerini fark ettim. Bu diyaloglar önceki günlerde gözümden kaçmıştı. Gardiyanların vücutlarının büyük bir kısmı dış duvar tarafından kapalıyken oradaki minicik hareketi görmemin tek sebebi sürekli sabit bekleyen odağımdı. Akşam 22:00, devriyedeki gardiyanlar dışında kimseyi görmedim. Zaman geçtikçe odağımı binanın dokularına doğru kaydırmaya başladım. Bütün mekânı saran, koridorları ve hücreleri birbirine bağlayan çatının dokusunda geziyordum. Merceğin maksimum yakınlığında ölçek hakkındaki bilgiyi kolayca kaybedebiliyorsunuz. Ay ve spot ışıklarıyla yıkanan çatı, boyutunu kaybetmiş siyah beyaz grafiklere dönüşmüştü. Sonu olmayan bir grafik gibi çatı, odağımın dışından bulunduğum daireye, oradan da gökyüzüne doğru uzayabilirdi. Çevrede kimseyi görmezken yaklaşabildiğim hangi doku varsa içinde kaybolmuştum. "),_c('inline-image-link',{attrs:{"image-i-d":'MOV_1641'}},[_vm._v("Gece boyunca")]),_vm._v(" dokuları incelerken 03:00’da metro yolunu kullanan bir araç gördüm. Aracın arkasına bağlanan römorkun içerisi beyaz çuvallarla doluydu. Taşıma sürecine eşlik eden iki otomobilin uyarı lambaları yanıp sönerken araçlar durdu. Metro yolu ve otoyolu bağlayan boşlukta beklediler. Yaklaşık 5 dakika sonra bir kamyonet, vagona paralel bir biçimde park etti. Araçlardan inen görevliler, çuvalları kamyonetin içerisine doğru atmaya başladılar. Çuvalların ağır olduğu çalışanların vücut hareketlerinden anlaşılıyordu. Sıralanan çalışanlar oluşturdukları çizgide ağırlığı bir sonrakinin kucağına aktararak taşımayı sürdürdü. Her bir çuval için geçen zamandan dolayı uzun bir işlem olacağını düşündüm. İçlerinde taşınan her ne ise hiçbir fikrim yoktu, sadece ağır olduklarını biliyordum. Bu süre boyunca kadrajımdaki her şey kırmızıyla yıkandı; çuvallar, çalışanların kıyafetleri, kaldırımlar ve kanalizasyon kapakları -yayalara sürekli yanan "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_3428'}},[_vm._v("kırmızı")]),_vm._v(" dur ışığı hiç değişmedi. Donakalmış kırmızı ışık, gece boyunca olmayan yaya trafiği nedeniyle sürekli olarak araçlara izin veriyordu. Mercekten uzaklaştığımda asfaltın boylu boyunca yeşil parladığını fark ettim. 4:15’te nakliye işlemi bitti, şu ana kadar doğrudan izlediğim durumun sesini net bir biçimde ilk defa duyabilmiştim, bu saate aramızdaki boşluğu dolduracak başka hiçbir ses yoktu, her bir yankılanma ve çınlama odağıma aitti. ")],1),_c('span',[_vm._v(" Gece yarısı metro yolunda izlediğim nakliyattan sonra uykum gelmişti. 2 saatlik uyku sonrasında sabah 7:00’da uyandım. 4 gün boyunca uyuduğum saatlerin yetersizliğinden dolayı uyanmam biraz zaman aldı. Dairenin cephesi boyunca sıralanan camların üzeri su damlalarıyla kaplanmıştı. Dışarıyı net görmek için pencereyi açtığımda yağmurun yoğunluğunu anlayabildim. Bu dairede kaldığım süre boyunca en şiddetli halinde yağıyor. 7:20, açık camların içeri taşıdığı havayla gittikçe soğuyan odamda uyuduğum ısıdan uzaklaşmak beni iyi hissettirdi, üzerime hırka aldıktan sonra odağımı çevrede gezdirmeye başladım. Cezaevi sakin, yollardaki arabaların sayısı artmaya başlıyor. 1 saat içinde trafik artmaya devam etti. Saat 8:20’de 2 kişi ellerinde tuttukları poşetlerle bina kapısından giriş yaptılar, yağmur nedeniyle hızlı adımlarla ilerlediler. Çalışan ya da nöbeti biten kişilerin görev değişim saati bu aralıktaydı. 9:00’da tekrar 2 kişi ellerinde poşetlerle giriş yaptı, yağmur şiddetini gittikçe arttırıyordu. Görüntü alabilmek için açtığım camdan gelen yağmur sesi bütün diğer sesleri bastırmış ve ince yağmur taneleri görüntülere doku eklemişti, trafikten yükselen araba seslerini neredeyse hiç "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2709'}},[_vm._v("duymuyordum.")]),_vm._v(" 10:30, her zamanki gibi araç kapısı açıldı ve çöp toplama aracı içeri girdi, yağmur nedeniyle çöpler nakliye alanının hemen önünde çatıyla korunan alana taşınmaya başlandı. Terasa çıkacağım zaman sıkı bir korumaya ihtiyacım olacağını düşündüm. Çöp aracı, biriken çöplere yaklaşmak için bahçede manevralar yaptıktan sonra yükleme işlemi başladı. Plastik çöp poşetleri yıkanan yağmurla birlikte parlıyor ve içi hakkında hiç bilgi vermeyen bu poşetlerin üzerindeki her bir damla parlamalar sayesinde dürbünümle seçilebiliyordu. 15 dakika süren yükleme sonrasında araç ayrıldı. 10:45’te önümdeki otoyol üzerinde cezaevinin logosunu taşıyan 2 araç gördüm. Bu araçları her görüşüm, onların benim izlediğim kapıdan gireceği anlamına gelmiyordu. Birden fazla sayıda konumlanan araç giriş kapısı, otoyolda gördüğüm bu araçları birer ihtimale dönüştürüyor. 11:05’te 3 gardiyan devriye yolundaydı, yağmurun sıklığı gardiyanların turlama ritmini etkilememiş ve saatlerde bir oynamaya sebep olmamıştı. Her biri üniformaları ile sıkı sıkıya korunuyordu. "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2826'}},[_vm._v("Avlularda")]),_vm._v(" şu ana kadar kimseyi görmedim. 11:30, ambulans içeri giriş yaptı, gardiyanlardan biri aracın yanına gitti, bir süreliğine şoför ile gardiyan konuştuktan sonra araç dışarı çıktı, hiçbir nakliye yapılmadı. Ambulansın yanına giden gardiyanı taktığı kapüşonu nedeniyle tanıyamadım. Benzer muğlaklık otoyolda da geçerli, kaldırımlarda yürüyen herkesin şemsiyesini ancak renkleri ve desenleri üzerinden takip edebiliyorum. Hareket eden yüzlerce daire yer değiştiriyor, üstü üste biniyor ve birbirlerine çarpıyorlardı. Sıkça karşılaştığım aynı renkteki şemsiyeler ise kolayca kafamı karıştırabiliyor. Odaklandığım kişilerin ifadesini ve yüzünü göremiyorum ancak hızlı ya da yavaş yürüdüklerini söyleyebilirim. Bir süre daha caddeyi izledikten sonra kahvaltı hazırlamaya başladım. Kendime hızlıca sandviç yaptıktan sonra camın kenarına geçtim. Açılan camdan içeri giren soğuk nedeniyle hırkamın kapüşonunu kafama geçirdim. 11:47’de 3 kişilik bir ziyaretçi grubu dış kapıdan girerek binaya doğru hızla koştular. Ellerindeki kağıtlar ıslanıp şekil değiştirmişti. Koştukları için kayıt alamadım fakat anne, baba ve çocuktan oluşan bir gruptular, çocuğun yaşı 16-17 arası olabilir. Ellerindeki ıslanmış kağıtla hızlıca girişe ilerleyip "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2923'}},[_vm._v("kırmızı paspasa")]),_vm._v(" ayakkabılarını sildikten sonra içeri girdiler. Saat 12:26, gardiyanlar yağmur nedeniyle gözcü kulübelerinin iç kısmına çekilmişlerdi. Bazı kulübelerde iki kişi varken, bazılarında tek kişi gözcülük yapıyor. 2. kulenin penceresinde hareketlilik gördüğüm için odağımı "),_c('inline-image-link',{attrs:{"image-i-d":'MOV_2924'}},[_vm._v("kuleye kaydırdım")]),_vm._v(" Kulede tek kalmış gardiyan dans ediyordu. Radyoda azalan sinyalden olsa gerek, radyonun anteninin yönünü değiştirirken dansını sürdürüyordu. Aşağıdan bakan biri için yüksekteki gardiyanı görmek uzaklık ve sağanak yağmurun etkisiyle neredeyse imkânsız. Gardiyanın dansı bana şemsiye ile dolaşırken yaşadığım bir hissi hatırlattı, genellikle kendimi yağmurlu bir havada şemsiyeyle gezerken daha şımarık hissederim, ifadelerimi düşünmeden kullandığım bir şımarıklık. Bu güvencenin en büyük etkeni ise, ikimizin de az yoğunluktaki deniz tarafından sarılmış olması. Burada kaldığım sürede ben de dans ediyordum, çevreyi izlediğim için benim dans alanım camın kenarıyla sınırlıydı. Gardiyanın dansı 10 dakika sürdü, radyoda ayarlamalar yaptıktan sonra sandalyeye oturdu. Gardiyanın oturmasından hemen sonra 2. avluda sıkı giyinmiş mahkûmu sigara içmeye çıkarken gördüm. 2 gün öncesinde avlu masasında toplanan kişilerin arasından hatırlıyorum, yaklaşık 40-50 saniye görmüş olabilirim. Sigarasını bitirdikten sonra hemen içeri girdi. Saat 13:02’de bir askeri aracı kapıda gördüm, bu sefer içinin dolu olduğu pencerelerinden anlaşılabiliyordu. Çöp aracı gibi, nakliye alanına iyice yaklaştı. İçinden 7 mavi üniformalı gardiyan çıktı, ikisini tanıdığımı düşündüm. Araç geldiğinde devriyedeki gardiyanlardan biri karşılama için aracın yanına koşmuştu. Dün kapüşon taktığı için azarlanan gardiyana benziyor. Araçtan çıkan gardiyanların bir kısmı içeri girerken bir kısmı da yağmurluğun altında sohbet etmeye başladı. 5 dakika sonra, 11:30’da gelen ambulans, askeri aracın yanına yanaştı, iki aracın yan yana durması duvara benzer bir biçimde görüntümü kısıtlamıştı. Kapıda gri kazaklı birinin beklediğini görebiliyordum. 7 gardiyandan 5’i gri kazaklı, orta yaşlı mahkûmla birlikte askeri araca bindi. Ambulansa ise siyah şişkin montlu biri bindirildi, ona eşlik eden iki kişiye avlularda hiç rastlamamıştım. Ambulansın içine en son sağlık görevlisi ve 2 gardiyan bindi ve araç hareket etmeye başladı. Askeri araç ise ambulansın arkasından çıkış yaptı. Ben de terasa çıkmaya hazırlanmıştım, bir poşet bulup merceğin geçebileceği boyutta kestim. Dışarı çıkmaya hazırlanırken apartman boşluğundan gelen ses dikkatimi çekti, kapı gözüne yaslanıp dışarı doğru baktım. Kapı gözünün kadrajı son 5 gündür baktığım kadrajdan farksızdı. Kapıdaki sessizliğim ve hareketsizliğimle birlikte günlerdir yaptığım eylemden hiçbir fark hissetmedim. Apartman boşluğundan gelen sesin sebebi kat temizliğiydi. Her biri eldivenli 3 temizlik görevlisi farklı temizlik araçları kullanıyor. Biri elindeki leğenle sabunlu su döküyor, temizlik makinası olan diğeri üzerinden geçiyor ve son kişi zemini paspaslıyordu. Kıyafetlerinin renkleri dürbünümde karşılaştığım üniformalarınkine benziyordu. Temizlik makinesi "),_c('inline-image-link',{attrs:{"image-i-d":'YYYYY'}},[_vm._v("daire kapısına")]),_vm._v(" oğru yaklaştıkça sabunlu suyun kokusu kapı altından bulunduğum holü sarmıştı. Temizlik bittikten sonra hazırladığım eşyaları alıp terasa çıkacaktım. Saat 13:30’da biten kat temizliğinin ardından terasa kurulmuştum. Yağmur şiddetliydi, şimdilik kestiğim poşet koruma sağlıyor. Kurulduktan yaklaşık 10 dakika sonra içeri gri renkli sivil bir araç girdi, içeri sivil araçların girdiğini görmemiştim. Aracın içerisinden çıkan kişi arka koltuğundan ambalaja sarılmış kâğıt havlular çıkardı. O esnada, nakliye alanına yakın kapılardan biri açıldı ve içeriden çıkan siyah kazaklı çalışan ambalajları taşımaya yardım etti. Bu giriş kapısı daha önce hiç açılmamıştı, cezaevinde çalışanların bir kısmının konakladığı alanın bu kapıyla ilişkili olduğunu düşündüm. Kâğıt havlular taşındıktan sonra araç çıkış yaptı. Saat 14:10 olduğunda, 1. avluda çiçeklere bakan kahverengi kazaklı mahkûm yürüyüşe çıktı. Dünkü hava durumundan dolayı çıkmadığını düşünsem de bugün hava daha yağışlıydı. Avlunun merkezindeki bitkiler yapraklarının çoğunu dökmüş, yağmur ve rüzgârla birlikte dökmeye devam ediyordu. Otoyolun arkasında gördüğüm tepedeki ağaçların yaprakları yok denecek kadar az. Avlunun merkezinde duran bitkiler, ritimlerini yerküredeki her "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_1301'}},[_vm._v("ağaç")]),_vm._v(" gibi koruyup dışarının ve içerinin bilgisini birbiriyle paylaşıyor. Bense, onların durumlarını anlamak için birine bakmanın yeterli olduğunu düşündüm. 1. avlunun merkezine odaklanmışken, yaya girişinden ön bahçeye doğru ilerleyen birini gördüm. Üzerinde uzun bir pardösü ve siyah kumaş pantolon vardı. Kıyafetlerinin kesimine ve oranına özen göstermiş biriydi. "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2612'}},[_vm._v("Siyah şemsiyesiyle")]),_vm._v(" birlikte yavaşça bina kapısına doğru ilerledi. Bir eli şemsiyede diğer eli cebindeydi, evrak tutup tutmadığını cebindeki eli nedeniyle göremedim. Ziyaretçi olduğunu nedense düşünmemiştim, güvenlik kulübesinde kontrol için durdurulmamıştı. 14:25’te 7. ve 8. avluya eş zamanda giriş yapıldı, 7. avluda yürüyüş yapan iki arkadaş ve 8. avluda önceden koşu yapan mahkûm belirdi. 7. avludaki arkadaşların yürüme süreçleri bu sefer daha hızlı ve seriydi, 5 dakika sonra daha ıslanmamak için içeri girdiler. Avluların çatılarının sağladığı siper çok azdı, dolayısıyla gördüğüm kişiler dışarıda çok beklemeyip hızlıca içeri giriyorlardı. 8. avludaki koşan kişiyse bugün hava almaya çıkmış gibi görünüyordu, bir süre gökyüzünü izledikten sonra içeri girdi. Avlulardaki mahkumlar koğuşlara girdikten sonra kimseyi görmedim. Saat 15:17, içeriye beyaz servis minibüsü girdi, aracı içeride karşılayan bu sefer müştemilat binasının kapısından çıkan görevliydi, gardiyana eşlik ettikten sonra binaya açılan nakliyat kapısına yöneldi. Devriyede görevli olan, genellikle nakliyatlara eşlik eden genç gardiyan hızlıca aracın yanına geldi. Nakliyat kapısı açıktı ve araç, içerinin görünmesini engellemiyordu. İçeriye odaklandığımda üzerine beyaz floresan ışığı düşen karo granit zemin görünüyordu. İçeride görevliyle konuşması biten genç gardiyan araca girdi ve minibüs çıkış yaptı. Araçtan inen gardiyan nakliye kapısını kapattı. Kapı kapandığı gibi yanımda tıkırtılar duymaya başladım, bu ses terasın kilitli olan, kullanmadığım kapısından gelmişti. Dürbünümü hızlıca başka bir yöne çevirdim ve kapı açıldı. Terasa yaklaşık 5 kişi girdi, içlerinden biri takım elbise giymiş bir yetkiliydi. Görevliler ne yaptığımı sordular. Önceden, böyle bir senaryoyla karşılaşacağım ihtimalini var sayarak, cümlelerimi düşünmüştüm. Ben açıklama yaptıktan sonra yetkili, teras temizliğine geldiklerini ve genel bir temizlik kontrolü için burada olduklarını söyledi. Benim açıklamamdan sonra, dürbünü daha fazla kullanabilmem için site yönetimine benim adıma haber verebileceğini söyledi. Ben de böyle bir izin için hiç kimsenin sorumluluk altına girmek istememesinin doğal olacağını ve kolay yoldan “hayır” deneceğini belirttim. Arkadan gelen temizlik görevlilerinden biri terası gördüğünde “olmaz böyle şey” dedi. Bu sürecin nasıl devam edeceğini tahmin edebiliyordum. Onlar telefonun başındayken eşyalarımı toplayıp gitmeye hazırlanmıştım, en azından bu diyaloğu daireme taşımak istemediğim için bilgi vermek istemiyordum. Ben herkese veda ederken, telefonla konuşan yetkili daire numaramı sordu, ben de ilişkisiz bir daire numarası söyleyerek oradan uzaklaştım ve daireme doğru yola çıktım, arkamdan tekrar soru sormadılar. Bu saati diğer temizlik saatleri dışında not aldım, bu saat aralığında terasta olduğum sürece dikkatli olabilirim. Ertesi gün buradaki konaklamamın son günüydü. Çıkış yapana kadar süremin çoğunu dairede geçirmeyi düşündüm, en azından bu kadar yakın bir zamanda yaşadığım diyalog sonrasında tekrar görünür olmak istemedim. Saat 16:10, terastan daireye gelmemin üzerinden 10-15 dakika geçti, terastaki karşılaşmamdan sonra dürbüne tekrar bakmak beni sakinleştirmişti. Bir süre cezaevinde kimseyi görmezken, genellikle her gece bilgisayarıma yedeklediğim görselleri yedeklemeye devam etmek için bilgisayarımı açtım. Bilgisayarımın şarjı azdı ve şarj etmek için taktığım adaptörün çalışmadığını fark ettim, kablomun zamanla aşındığını sürekli görebiliyordum fakat kullanım anlamında ilk kez işlememeye başlamıştı. Bir anlık, dosyalarımı nasıl aktarırım diye düşünürken, adaptörün kablolarıyla oynamaya başladım. Dürbünün netlik ayarına benzer bir işçilikle kablonun içindeki bakır telleri belli bir açıda tutup bantladım, sonrasında duvara doğru bastırıp şarj ışığını gördüğüm bir anda sabitledim. Şarj aleti çalıştıktan sonra kabloya yarına kadar hiç dokunmayı düşünmüyorum. Saat 16:30, 2 ziyaretçi çıkış yaptı, terasta yaşanan durumdan sonra girişlerini kaçırmış olabilirim. Yağmur şiddetini azaltsa da yoğun bir biçimde devam ediyordu. Otoyollarda oluşmuş su birikintileri gittikçe derinleşmiş, önümdeki inşaatta çamura dönmüş toprak alanlar yavaşça inşaatın temelindeki boşluklara sızmıştı. 2. avluda hava kararmaya yakın, gri boğazlı kazağı olan gencin sigara içmek için dışarı çıktığını fark ettim; 2 gün önce bu saatlerde duvara çok yaklaşarak turlayan mahkûmdu ve yanında birileri yoktu. 2. avluya çıkan mahkûmları genellikle grup halinde görürdüm, aralarında belirli saatlerde avluya yalnız çıkan bu genç mahkumdu. Yakın zaman aralığında 5. avluda duran sandalyeler lacivert spor eşofmanlı biri tarafından içeri alındı. Avlularda, üzerinde minder olan bütün sandalyelerin minderleri yağmurdan renk değiştirmişti bile. Saat 17:00’a yaklaşırken, uzaktaki otoyolun yol ayrımındaki iki fosforlu "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2640'}},[_vm._v("üniformalı")]),_vm._v(" görevli dikkatimi çekti. Yaklaşmaya başladığımda, görevlilerin destek alarak kaldırdıkları ince metal bir iskelet gördüm. Ne olduğunu anlayamadım, bir su giderinin kapağı olmak için çok büyüktü. Çalışanlardan biri kenara çekildiğinde, iskeletin ortasından kesilmiş, yolun kenarındaki oluğun sığacağı kadarlık boşluk gördüm. Su oluğunun çevresini ya da yolunu tıkayabilecek parçalardan koruyan bir iskeletti. Görevliler iskeleti yerleştirdikten sonra araca binip uzaklaştılar. Hava kararmaya başlamış ve yağmur şiddetini tekrar arttırmıştı, iki tarafın ışıkları her an yanabilirdi. Acıkmaya başlamıştım, bugünkü yaşadığım diyaloglardan sonra aklımdan çıktığı kesindi. Dolaptan konserve ton balığı ve biraz ekmek aldıktan sonra camın yanına geçtim. 18:45’te 2 kişi ellerinde poşetlerle dışarı çıktı. Giriş ve çıkış yapan çalışanları genellikle tanıyamıyorum, fakat bu sefer 2 gün önce nakliye süresince gördüğüm sağlık ve temizlik görevlilerini üniformasız tanıyabilmiştim. Hızlıca araç girişindeki kapıya doğru ilerlediler. Girişte bekleyen güvenlik kulübesinin ışıkları eşzamanlı olarak açılmıştı. Bir süre sonra iki çalışanın dış kapıdan geçtiğini gördüm. Saat 19:05, odağımı en uzaktaki gözcü kulelerine çevirmiştim. Uzaklıkları nedeniyle kadrajım kuleyi bütün olarak alıyordu, içlerindeki gardiyanlar hakkında not alabileceğim tek bilgi sayılarıydı. Böyle bir uzaklıkta not aldığım şeylerin daha sayısal veriler olması olağandı, gördüğüm her ne ise birbirine benzer hacimlere dönüşmüştü. Rüzgâr, gittikçe uzakta sabitlenen kadrajı katbekat daha fazla titretmeye başladı. En uzaktaki kulede 2 kişi, onun önündeki kuledeyse 1 kişi gözetimde görevliydi. Ara uzaklıktaki kuleye spot ışıkları düştüğü için kulenin iç çeperindeki beyaz duvara yazılan yazıları lekeler halinde gördüm. Uzun cümleler şeklinde birbirini takip eden yazılar genellikle duvarın kenarlarında sıklaşmış ve kulübenin döndüğü kenarları takip ederek iki farklı yöne doğru uzamıştı. Saat 20:15, devriye gardiyanları her zamanki yerinden geçiyorlar, sırtlarında yazan “jandarma” yazısı spot ışıklarıyla iyice parlıyor. Odağımın merkezini ışığın kaynağına doğru çevirdim. Daire biçimindeki kadrajımın tam merkezine aldığım yatay yoğun ışık kümesi hem gözümü aydınlıktan yormuş hem de dokümantasyon için bağladığım telefonumun ekranını bembeyaz yapmıştı; beyaz ekran bulunduğum karanlık odanın büyük bölümünü bir anlığına aydınlattı. Orada kontrolü sağlayan ışık, görevini daireme taşırarak devam etmiş ve bulunduğum odayı "),_c('inline-image-link',{attrs:{"image-i-d":'MOV_3039'}},[_vm._v("dikkat")]),_vm._v(" edilmesi gereken ikincil alana çevirmişti. Hızlıca odağımı çevirerek devriye yolunu izlemeye devam ettim. Saat 20:42’de, 1. avluya bağlı pencerelerin birinde gölgeler gördüm, bu pencerenin koğuşlara değil ama ara koridora açılıyor olma ihtimali var. En azından bu bilgiyi bağlı oldukları çatını