ükten sonra bir süreliğine yerlerinde beklediler. 9:25’te içeriden gelen sarı kazaklı mahkûm kapıya en yakın ambulansa bindirildi. Nakliye tamamlandıktan sonra ambulans beklemeye devam etti. 3 dakika sonra nakliyat kapısından yaşlı biri yavaşça çıktı, gardiyanlar mahkûma yol boyunca yardım ediyorlardı. Buradaki zamanım boyunca gördüğüm en yaşlı mahkûmlardan biriydi, gardiyanların desteğiyle 2. ambulansın içerisine oturdu. İki ambulans için acil bir durum söz konusu değildi, süreç boyunca kimse acele etmemiş ve sirenlere gerek duyulmamıştı. Yaşlı mahkûma eşlik etmek için gardiyanların bir kısmı ambulansa girdi, diğerleri de bahçede bekledi. Kapı açıldıktan sonra ambulanslar dışarı çıktı. Saat 10:10, çöpler her zamankinden daha erken bir saatte dışarıda hazır bekliyor. Bina içerisinde kullanılan "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_3631'}},[_vm._v("süpürge ve faraşlar")]),_vm._v(" dışarı çıkarıldıkları esnada binanın duvarına boylu boyunca dizildi. Her biri yeni yıkanmış ve kurutulmak için güneşte bırakılmıştı. 15 dakika sonra çöp aracı içeri giriş yaptı. Araç çıkış kapısına paralel bir biçimde bekliyordu, bahçenin köşesine bırakılan büyük poşetler taşınırken içeriden daha fazla sayıda çöp gelmeye başladı. Bugün çıkarılan çöpler genel bir temizliğin sayısız atıkları gibiydi, opak siyah poşetler nedeniyle içlerindekini görmek imkansızdı. Benim çöp kovamdaysa teneke konserveler, hazır pizza kutuları ve yanmış bir yumurta vardı. Çöp aracı giriş kapısına paralel park ettiği için üzerindeki yazı ve grafikleri inceleyebilmiştim; yeşille boyanmış yüzeyin kenarlarında pembe çiçek desenleri ve merkezinde beyazla yazılmış “Eviniz Kadar Temiz” cümlesi yer alıyordu. Toplamda 25 dakika süren çöp nakliyesi sonrasında çöpleri taşıyan 5 görevli, duvara yaslanmış temizlik araçlarını alıp içeri girdiler. Çöp aracı depo haznesindeki pistonları çalıştırarak çöpleri sıkıştırmaya başladı, bana kadar şiddetle ulaşan tiz bir sesle deposunu dolduran bütün çöpleri bir anda yok et