rladım, aynı zamanda daireyi temiz bırakmam gerektiğini. Gözetlememi hem salondan hem de yatak odasından sürdürdüğüm için mobilyaların yerlerini ve kullanım hallerini değiştirmiştim. Üst üste binmiş eşya kutuları günlüğümü taşırken, bazı sandalyeler, üstüne çıkarak kadrajımı ayarladığım yükseltilere dönüşmüştü. Yemek yerken izlemeyi sürdürdüğüm sehpa ve koltuk dış cama bitişik duruyordu. Bir ev formatında dairesel ve karşı karşıya duran bir seri mobilya yerine çizgisel ve yüzünü cama doğru çevirmiş, bir sahne gösterisi için sıralanmış koltuklara benziyorlardı. Temizlik sürecinin kaydını aldıktan sonra daireyi toplamaya başladım. Saat 14:30, eşyaları yerleştirdikten sonra bulutlar çekilmiş, güneş avlulara ve bahçeye düşmeye başlamıştı. 2. avluda mahkumların en genci olan gri kazaklı, avlunun güneşin düştüğü kısmına ilerleyip bir süre duvara yaslanarak bekledi. O esnada 3. avluda ikinci gün top oynarken gördüğüm mahkûmlar sandalyelerini güneşe doğru çekmiş sohbet ediyorlardı. Turuncu tişörtlü olan sohbet sonrasında avlunun bir kenarından diğer kenarına ip gerdikten sonra içeriden getirdiği "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_0995'}},[_vm._v("pantolonu")]),_vm._v(" astı. Pantolonu, merceğimin en uzak ayarında havada uçan bir işarete benziyordu, yakınlaştıkça ölçeğini ve dokusunu anlayabildim. Avludakiler içeri girdiğinde pantolon rüzgarla sallanmaya başladı. Çıkma zamanım iyice yaklaştı, odağımı yolun kenarında durmuş bir kamyona çevirdim. Kamyonun üzerindeki turuncu branda yanındaki inşaat alanına parça bırakmak için açılmış ve büyük bir kısmı otoyola taşmıştı. Bu kocaman turuncu leke yoldaki şeritlerden birini kaparken, araçlar yönlerini değiştirmek üzere sinyal vermeye başladılar. Kısa süren kargaşa boyunca korna sesleri dairemden duyulabiliyordu. Şoför, hızlıca yola çıkarak "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_3847'}},[_vm._v("brandayı")]),_vm._v(" toparlamaya başladı. Kamyonun üzeri geri örtüldüğünde önceki halinden hiçbir iz kalmamıştı. Dairedeki mobilyaların yerlerini tekrar kontrol ettim. Dürbüne döndüğümde odağımı son kez çevrede gezdirmeye başladım, bir anlığına merceğime parlayan, cezaevinin çatısının üzerine kurulmuş paratoner dikkatimi çekti; toparlanmadan hemen önce odağımı tekrar sabitlemiştim. Dürbünü toparlayıp bavula koyduktan sonra taksiye atladım. Aklıma paratoner takılmıştı, havanın kötü olduğu süre boyunca manzarama hiç yıldırım düşmemişti, belki de bulunduğum binadaki paratonerler her şeyden önce yıldırımı üzerine çekmişti. Fiziksel yükseklikle değerlendirilen bu formülde bulunduğum binanın yüksekliğiyle çevremdeki yapılar eşleşmiyor. Benden daha yüksekte olan diğer her şeyin bütün enerjiyi kendi üzerlerine çekeceği kesin, aynı zamanda gökyüzünün hedefi olacağı da. Bu dikkat çekici parlaklığı bir felaketten ayıran tek şey ucu sivri, "),_c('inline-image-link',{attrs:{"image-i-d":'IMG_2777'}},[_vm._v("metal bir parça")])],1),_c('div',{attrs:{"id":"section-6"}}),_c('AmpersandSeparator'),_c('AmpersandSeparator')],2)}
