var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"document"},[_vm._l((_vm.activeImageWindows),function(activeImageWindow){return _c('image-window',{key:activeImageWindow.imageID,attrs:{"windows-image-object":activeImageWindow}},[_vm._v(_vm._s(activeImageWindow))])}),_c('div',{attrs:{"id":"section-1"}}),_c('span',[_vm._v(" It’s 7:05 a.m., it’s cold outside, the roofs are shiny, it must be because of the dew. I am unable to see the outside clearly through the window, the change in the weather steamed up the surface of the glass. I’ve started my day by clearing the cloudy steam on the window. Though I could take a picture by cracking the window open, I wanted to follow what’s going on through a larger opening. The reason why I woke up at 7 was that I have read from a not-so-reliable source that many people in prisons start their day around that time. I started to move my lens in a half-asleep mode, it was a stroll in which I tested the boundaries of my view. Highway, metro stations, a large overpass where roads connect, a green hill on the horizon, and "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0516"}},[_vm._v("buildings")]),_vm._v(" of various heights around it. From the flat I am in, I can see the boundaries of the prison, separated by the road. The northern edge of this land, a rectangular strip, can be seen barely by my scope. The southern edge, on the other hand, is parallel to my flat, another border of the land that stretches to just beneath my feet. The double outer wall and the walls of the courtyards to which every ward is connected are very high. From my position, this height is unimportant. From where I stood, I could see all eight wards, the large gateway in front of the building’s entrance, and the car entrance it is connected. The farther the courtyards, the harder I could see what was inside, and towards the ends, they became repetitive cornered patterns. Approximately 15 minutes after I started to move my lens, I saw "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1926"}},[_vm._v("two guards")]),_vm._v(" in their blue uniforms when I focused on the outer wall. It was usual to have patrols around the building. The route they were following was on the area between the double outer walls. This two-layered structure allows circulation by providing a gap of 3 or 4 meters in between. It’s 7:40 a.m., I haven’t encountered any activity after the patrol. In each of the five watch towers I am able to see, a guardian stands. While everything around is calm, sun rises and shines, the light slowly seeping into each courtyard got right and straight while drawing splitting lines inside the aligned rectangular areas. The shadow on each courtyard was imitating one another. At 7:46, someone passed through the door next to the car entrance and reached to the building entrance, he was carrying a plastic bag. Since it was completely bright outside, I started to see inside the watch towers clearly. In 30 minutes, four other people entered. In those entries that were close to each other, all the visitors had plastic bags in their hands, I thought they were carrying the uniforms they were to wear inside. They could not be visitors because, according to the information given on their web site, the visiting hours were limited to week days, the weekends were reserved to phone calls. One of the two people entered together had a black uniform while the other was wearing a red-black striped coat. They entered while chatting, they must be friends. Just after them, before the door was closed, came another person wearing "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0417"}},[_vm._v("a dark green coat.")]),_vm._v(" After them, around 8:30-8:40 a.m., three people walked out the building door, one had a green sweater that could be identified easily by naked eye, all three had bags. They did not resemble those three entered an hour before, maybe their night watch ended and they changed shifts with morning team. 9:00 a.m., it’s calm outside, only a couple of people has been to the front yard. The only place other than courtyard is this garden for people who are working in the main building to have some fresh air. The courtyards were quiet, I hadn’t seen anyone, maybe everyone was in breakfast, I was hungry too. Cornflakes and milk were my breakfast; I eat cornflakes at home if I am in a hurry. It must be the excitement of the first day, I didn’t want to lose sight even for a second. At 9:30, one in uniform and one in plain clothes, two people were walking on the patrol route. The one in plain clothes was holding a leash connected to a restless dog. Along the route, two men plated with the dog, raced with it back and forth; energetic dog was swinging the owner to more than one direction. Focusing from such a distance makes the traces of the object’s activity even more graphic – both advanced on my viewfinder together with "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0444.JPG"}},[_vm._v("the same blurriness.")]),_vm._v(" When they reached just beneath the closest watch tower to me, two guards at the tower "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0450"}},[_vm._v("watched")]),_vm._v(" them smilingly as they stretch out to the side from the balcony of the tower. After a talk from tower below, patrolling kept his route while dog and its owner returned. After everyone left my sight, a long calmness stepped in. In turn, the horns coming from the morning traffic directed my attention to the highway. For a while, I watched people coming and going to the work, catching the subway and moving within the traffic. It’s almost 11:00 a.m. now, the door inside the first courtyard opened and closed, a man in brown sweater entered and exited the courtyard in no time as if just to check the courtyard. I have numbered the courtyard according to their proximity. Half of the eight courtyards divided into two lines is on the north and the other half is on the south. On the southern edge, the courtyards close to me were numbered as 1, 2, 3, 4, from right to left; on the north, 5, 6, 7, 8. There were one or sometimes, two doors opening to the courtyards. I thought that this was due to the different conditions of the wards that the courtyards were connected. Some of the metal doors were completely white, and the rest was sea green. I remember that the water turns to this color in shores where the sea is wavy or in ponds where there’s no stream. Likewise, the number of people entering the building is close to zero. In this tranquility, I have focused on the objects on the courtyards and moved my lenses around. Plastic tables and chairs, covers and cushions over them in various patterns, they were all exhibited on these areas of same volume, in different manners and in different places; one could be closer to the wall and embellished with floral cushions, another could be at the center of the courtyard, wrapped in chequered covers. When I turned my focus to the overpass and the hill on the horizon, I could not help but look at these courtyards folding inward and through its own center, even in this view where stretching highways, diversifying buildings and changing clothes fold continuously outward. The wind became stronger, the clouds changed shape with the breeze and the sun could only be seen from time to time. The high "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0868"}},[_vm._v("walls")]),_vm._v(" there must have blocked the wind at least in part, I am unable to tell exactly how strong the wind is since there’s no balcony in this residence flat. It’s 11:20 a.m. In groups of two, guardians coincided between first and second towers, they saluted each other. It seems that there’s a pattern for when the patrol comes into my sight. Someone in navy blue sweater came out of the first courtyard and called out inside, this took 2-3 seconds, I couldn’t get the chance to focus my lens. Right after, a middle-aged person with long hair and moustache started to walk close to the wall, he was so close to the wall that I could only see his hair at certain intervals. When he came close to the middle, I saw the red jumper he was wearing. It was bright red, separating itself sharply from the gray concrete of the floor, mostly fabric used for sportswear shines this much. When I zoomed in, I saw the white lines going down from the shoulder to the ankle, just as the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1777"}},[_vm._v("white lines")]),_vm._v(" on the highways surround us. After spending some time wandering around the courtyard, he came close to the wall and went inside. Since I had an almost right angle view, it allowed me to see parts closest to the walls – from the shared terrace of this building, some of the courtyards can be seen better from above with an angle approximately right. I thought I might go for a recon on the terrace to use two different angles. It’s noon. The door to the first courtyard was opened and left open. From the wheels, a steel frame on them and a bed seen just after the door was opened, I understood that a stretcher was coming to the courtyard. Of the two people pushing the stretcher, one was a guardian wearing a black uniform, and the other might be another inmate sharing the same ward. The person on the stretcher had a red beret and was covered with a blanket with the prison logo on it. The other inmate was older than the one on the stretcher, he was wearing a brown sweater, which I remembered from one hour before. The brown-sweater accompanied the stretcher until the edge of the courtyard and pulled a chair for himself. They didn’t talk for so long. The one with brown sweater nodded many times while the inmate on the stretcher at the other side of the table and went inside. At 12:30 p.m. black-uniform guard came to the courtyard and brought the man on the stretcher back inside and the courtyard door was "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1436"}},[_vm._v("closed.")]),_vm._v(" I decided to have some pasta upon seeing no action 5 minutes after the door was closed. As the action on my lens decreased, I thought, I was focusing on the action and hunger in my own body. I added tomato paste I kept in the fridge into the pasta and started warming, before starting to eat it, I had moved the couches and tables in the living room. There was no circular arrangement where pieces of furniture are facing each other, just as the seats before a film screen, the couch and the other furniture were facing the window, watching the outside. With their new position and my table leaning on the window, my view included the highway, the prison, the hills far away and other buildings "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0496"}},[_vm._v("at once. ")]),_vm._v(" It’s 1:25 p.m., the door of the first courtyard was opened once more, and the first one to walk out was the inmate with the brown sweater. He might be in his fifties, he walks slowly. Another inmate following him into the courtyard closed the door, they were more or less the same age. The latter had a black tracksuit top and a trousers of dune color. By the time the door was closed, the brown-sweater inmate had already reached the other end of the courtyard. As far as I could see, first courtyard has a special feature among the others. At the center of the courtyard, the soil wrapped with concrete allowed plant growing by serving as the flowerpot of the courtyard. Checking the flowers while they were walking, the brown-sweater inmate grabbed the plastic bottle leaning on the wall and watered them. I guess he’s taking care of the flowers more frequently than others. The one in black tracksuit top didn’t stop while taking a look at the flowers, he kept walking with the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1405"}},[_vm._v("same tempo")]),_vm._v(" . From time to time, he came closer to the wall and received support, and sometimes, he walked towards the center of the courtyard. I started to move my lens between courtyards and the front yard and focused each and every one of the people I saw. Sometimes, when I close up to the faces, I think that they see me as well. It’s a physical impossibility to be seen at this scale, both due to the distance and the angle. Lenses focusing on the distance play games on my eyes which could only have blurred visions of the distant. This visual unaccustomedness must be another game that makes me feel suspicious about being seen. Zooming in at this scale feels like falling into the focus itself. It’s 1:42, as brown-sweater inmate was moving towards the door, he came across someone on a wheelchair. This new person was younger and had long brown hair slicked back. After a short dialogue, they moved towards the ward’s window and started talking with someone inside. That didn’t last long, the brown-haired took only a couple of puffs "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0525"}},[_vm._v("during the conversation")]),_vm._v(" . The things happening on the ward side – which I wasn’t able to see – enhanced the scenes in my mind. The courtyard which was out of this and from which I gathered information directly was a rectangular place that stretches to the sky and shares the space in front of my flat. It’s 1:40 p.m., although briefly, the brown-haired inmate said something towards the ward through the window in each of his turns – he might be talking to more than one person. The lack of sunlight – because it could not enter properly through the courtyard windows – made the courtyard darker and turned the windows of the courtyard into black rectangles located at the center of my focus. The windows of all the flats here hides what’s inside and reflects the outside like a mirror with its half-filmed glass. Anyone taking a look from far could watch the landscape, the shining sun and the clouds on these buildings. Brown-sweater inmate in the first courtyard kept walking in his usual tempo, this time he was even closer to the window railings, he had been in move for so long, he might be tired. At that moment, the brown-haired inmate went out of sight, the other increased his pace and tempo. It’s 2:10, I understood that I paid much attention to the first courtyard when I zoomed out, I saw a group of cardboard boxes brought to the entrance, they must have been carried by a car. When I took a look again at the first courtyard, I zoomed in once more to the inmate who was still walking. This time, I was able to discern in detail the colors, his sweater was a knitting blending brown and grey, there were dark brown square patterns on it. He was wearing navy blue trousers separating his torso from the colors of the wall and the courtyard. More than half of his hair was white and in each step, his left hand was swung forward faster than normal. The information I had when I had first focused multiplied over after a long period of attention. As the details I saw filled my viewfinder, I thought, they came to life in my memory in a manner that I didn’t witness before; they belonged to neither the point I was looking at, nor the point that was looked at. It’s 3:00 p.m., the first courtyard is empty now, I didn’t see anyone in the other courtyards. Fifteen minutes later, a guard came to the third courtyard, took a quick look around and went back inside. The sun was coming in a right angle to my scope; thus, the vision on my focus was "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0561"}},[_vm._v("shining")]),_vm._v(" creating white stains. The lens, as it did to everything, magnified the power of sunlight and made it hard to look. The activity I saw in between the flashes was a short dialogue of two guards at the front yard. During their conversation, they moved towards the shadow to avoid the sun. However, I was detected and targeted by the sun together with the scope through which I could see only partly. I couldn’t see anyone at 3:55, so I decided to make myself a sandwich. Starting from 4:17 p.m., I witnessed the increase in number of people in the first courtyard. Three people were walking; I hadn’t seen these people before in the courtyard. At 4:17, first, someone with a navy blue sports jumper and someone with a jumpsuit, and later, two younger person came to the courtyard. One of the younger men was wearing red vest and under it, a red jumper. The reason he stayed in my focus was that he was, as opposed to others, walking fast and taking quick steps. He was walking towards the courtyard door and returning back in a quick manner and wandering around while his hands were in his pockets, without participating to the conversations of the others. While increasing his tempo, the person living upstairs blew in the window so hard that my focus setting was gone for a moment, it was actually a small vibration but even a small breeze could test the location of a tool which could see that far this clearly. With the vibrating image, I focused again by reminding myself the settings from the morning. At 5:00 p.m. three people walking met a guardian and some other person next to him at the white door of the courtyard, it looked like a briefing. After the conversation, the guardian showed the paper he was holding in his hand and told something to the inmate with navy blue jumpsuit, then everyone in the courtyard entered their ward and the guardian closed the door. Later on, the activity was observed on the outer parts of the courtyard, on the patrolling area 3 "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0620"}},[_vm._v("guardian")]),_vm._v(" came together, had a brief chat and then, moved to the northern part of the facility and went out of sight. Since I am able to see a part of this route, I witness a section from each guard’s patrol. Until now, I have encountered patrolling guards every 45-60 minutes. At 6:00 p.m. a light was "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1479"}},[_vm._v("turned on")]),_vm._v(" , on a window facing the first courtyard, it was getting dark, the room I was sitting in was lit enough, I didn’t consider turning the lights on. A little before 6:00 two people exited the building, one of them was the person with red-black striped coat, who entered in the morning. The courtyard lights were turned on around that time but the large spots were not yet turned on. The large spotlights were turned on at 6:15. At 7:10 p.m., the guards were seen on the patrol route once again, they were in groups of two. The fact that it was getting dark might well mean that the day was done for the courtyards. After 5:30 I have only seen guards at the yard and the patrol route. Another effect of the dark was that my priority of attention had changed. When I was focusing at a certain place at night, it got harder and harder to focus other parts that were darkened and the movements under the lights attracted my attention more and more. The light is so successful at distracting me while illuminating a certain area for me, I might have missed a couple of people who had stayed in the dark. I did not turn on the lights of my living room, I had decided to stay in the dark throughout the process, I had enough light coming from the streetlamps of the highway that lied in front of me, headlights embellishing the road and the prison spotlights. After a long and calm gap, at 8:17 p. m., 4 people left the building in groups of two and walked towards the garden door, only two had plastic bags and they were not the same people who entered in the morning. Three minutes later, someone ran and followed the route of the group, he might be trying to catch the same vehicle. At 8:30, two vehicles came to the entrance in front of the building after the sliding door was opened, one of them was an ambulance and the other was a military shuttle. The ambulance did not seem to be in a hurry; although it’s blue-red lights were on, there was no siren. There was a wide metal gate of the building in parallel to the car entrance gate. I thought, because of its scale and size, that it must be connected to an area that was used for "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0713"}},[_vm._v("transfer")]),_vm._v(" . Recurring layers of the door’s body, like materials strengthening in every fold, was keeping up this large piece of metal. From the ambulance, an old men carrying a similar plastic bag came out, accompanied by two guardians. From the military shuttle, five guardians came out together with another person they were accompanying. Even though it was dark, I was able to see that he was young and had black hair. When I focused inside through the open door, I could see that they got the stretchers and wheelchairs ready to carry people, but it was not such a case. After the transfer was completed, the guards came together and started to chat and smoke. Blue uniform officers were majority, during the transfer, two black uniform guards came out from the front door to join the others. Thanks to the headlights, I thought that I recognized some of the faces from the morning, two guards I described as younger from their patrol were there, also one of the guards that had watched the dog was there. The gathering ended at 8:53 when everyone went on their own way: the shuttle or their places of duty. Two vehicles moved to outside. Patrolling guards participated to the transfer walked towards the route that connects the towers. The stagnation reigned again, after I took a step back from the viewfinder, I had hard time to adjust to the darkness, whatever it was, the light on my focus was well-lit enough, yet I was "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0754"}},[_vm._v("waiting")]),_vm._v(" in the darkness of the room. In the darkness I got used to at my 11th hour in the living room, everything looked crystal clear, I remembered how my pupils dilated when I turned off the lights in front of the mirror. In order to follow the last transfer from a better angle, I had put my scope higher up and gone down on my knees on a stool and kept watching. But when I leaned on the couch and started to rest, I felt pain in my knees, I went and grabbed an apple from kitchen, and started eating it. At 10:05 p.m., the number of the patrolling guards increased to three. When I focused on watchtowers, I saw white stains in the darkness, these are the phone lights of the guards. At 11:10, I saw the patrol again, one of the two fell behind and started to run, the shadow emerged below "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0370"}},[_vm._v("the guardian who was waiting")]),_vm._v(" the one running was as visible as the guardian himself –only spotlights were working at that hour. It’s about to be midnight, I realized that it would be my 17th hour of the day in which I had got up at 7:00 a.m. For two hours in which I did not see a single soul, I watched outside from the window and hummed a tune. I was able to validate a series of tone and rhythm consisting of words and sounds with my own voice reflected from the walls of the room. It seemed like a celebration of a body that is unable to look far ahead. ")],1),_c('AmpersandSeparator'),_c('div',{attrs:{"id":"section-2"}}),_c('span',[_vm._v(" Right after I woke up, I went near the window, I had woken up at exactly the same hour with the day before. The car horns coming right up were from the early morning rush hour traffic. The pedestrians like a parade of similar tempos, the heat coming up from the engines, the sounds stuck within each other, they all became an exercise for everyone before an hectic day. I, too, became a part of this exercise by changing the focus quickly while coming closer to the moments dispersed all over the overpass landscape in front of me. At 8:20 a.m. three cars entered the prison, one was covered with camouflage paint of dark-light green and brown. The other two were white. The reason why I got attracted by the camouflaged car first was that it was easily distinguished among the plain, dull colored structural elements. In this environment it did not belong, camouflage looked like an ornament which was easy to follow along the road, and caught my eye. Two people from the building walked towards the shuttles, those were the same two that had been given lift to the building the night before. I think I remember the guards who got off the shuttles and the license plates. After two inmates carried to the car, another older inmate was transferred and the guards helped the old inmate while walking to the car. Until 8:50, shuttles didn’t move, the transfer process was still on the way. The young patrol guard whom I witnessed in similar transfers, and whom, I thought, was in transfer duty all the time, was among the shuttles that day as well. 5 minutes later, two young inmates were taken to the camouflaged car and the transfer vehicles left the premises, after they left, two guardians on the yard talked for a long time. I felt that my body needed a bit more rest, I was rubbing my eyes often, I thought that it was the tiredness why I wasn’t able to see clearly the contours of figures and objects in my room, but I understood that it was the fog surrounding me when I stepped away from the viewfinder. Large cloudy gas accumulated over the landscape in front of me and was moving slowly towards the highway under it. For a second, I remembered the dream I had the night before, mostly, the shapes of people and objects I saw in my dreams do not consist of a sharp contoured and detailed image, just like the foggy landscape I was just looking at. Who or what they are can be understood but my mind leaves some pieces empty and missing. The dream I remembered was in the same blurred manner. In the dream consisting of two pieces, I was hanging in the air, I was able to zoom in with naked eye as I could with my scope, and hear what was spoken, I was involved in the conversation but hadn’t spoken yet. While floating in the space I was hanging, listening to the conversations, I started to gyrate and then, I woke up. I don’t witness any surreal moments in my dreams; it’s like each of them are moments in which a small probability was changed and I witnessed a probable result. This dream was also similar to the others. While I was thinking about the dream, three vehicles returned, but this time a handcuffed "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0837"}},[_vm._v("inmate")]),_vm._v(" got on the military shuttle. The brown-sweater inmate who took care of the flower on the first courtyard got on the shuttle in which the medical equipment were apparent, he was not handcuffed. At 9:30 a.m. garbage truck entered, ten minutes before that employees brought the garbage can outside, it was a pile of orange trash cans and black plastic bags and it was waiting, leaning on the wall. Since I haven’t eaten too much for the last two days, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0860"}},[_vm._v("trash can")]),_vm._v(" at home was not even half-full. Garbage truck left at 9:40. The weather was different than the day before, after the fog dissipated, a freezing cold came and the sun’s effect was lowered to a great extent. The sliding entry door for cars, as far as I saw, was left open – another car could be here in short time. After fixing my focus on the door, I went to cook some eggs. At 10:10 a.m. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0922"}},[_vm._v("three guards")]),_vm._v(" were patrolling passing near to the watch tower that was closest to me, I had cooked three eggs. After the patrol was lost sight, I ate my breakfast quick during this quietness. At 11:07, a red truck came in, it moved backwards and turned its back to the loading area. The car was so close to the loading area, there was almost no gap between the car and the building, I couldn’t see what was loading, there were only two patrolling guards instead of three, maybe one of them went to help. I wasn’t able to see the back side of the truck, the reflection on its windshield caught my eye. From that reflection, the outer contours and the windows of the building I was in could be seen. The building, due to the partly-convex nature of the windshield, was distorted, and different from its original physical and structural features. It looked like the imaginary buildings that were outside the reality of the building complex I was in and I was at any of the distorted windows. It reminded me of my dream. I paused watching the reflection for a short time and went to grab my toothbrush. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0875"}},[_vm._v("The vehicle")]),_vm._v(" exited at 11:27. I brushed my teeth for almost 10 minutes. At 12:15, two shuttles entered. Two black-uniformed officers who got out of one of the vehicles entered the building. At the same time, a visitor entered via the front door. I thought that he was a visitor when I saw the papers he was holding and the long dialogue he had in the security cabin next to the entrance. He was wearing a black t-shirt and jeans, he was carrying his coat in his hands and he went "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0900"}},[_vm._v("inside")]),_vm._v(" hastily with the papers he was holding. I thought that the weather was getting colder when a breeze came into my window. At 12:36 p.m. the number of visitors increased in every 20 minutes, there were 4 guests spending time at the entrance yard. Two people were together and chatting while walking around at the yard. When I zoomed in and focused on the visitors, the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0928"}},[_vm._v("gray concrete")]),_vm._v(" which constituted the ground for everyone in my vision universe was repeating itself; I see the inmates at the yard, the guards patrolling and the visitors at the entrance on the same fabric. When it’s this close, the information I have about the environment is limited to a circle with a radius of 3-4 meters from the center of my focus. Whatever I was seeing while closing in the focus with my lenses, narrowing my view frame, was separating the courtyard, the garden and the patrol route. At 1:15 the ambulance entered and the young inmate wearing a gray sweater was taken inside together with the guards. It was quicker than the other transfers and ambulance was already out of sight. At 2:00 p.m., I decided to go to the terrace to see if I can see a wider area. I packed my stuff in a portable manner. But I took only the camera with me because I wasn’t sure about the angle on the terrace and if it was empty or not. When I was on the terrace, I thought that it must be a place that nobody used; the ventilation duct in the middle and a small-scale crane had already narrowed down the space. These scaffoldings that looked like the ones used in construction sites were used to carry the metal cages in which attendants could clean the window glasses surrounding whole building and maintain them. The presence of the crane on the terrace provided a special isolation as if this place was reserved only for authorized personnel. Although it’s a shared space, I felt good for the time I’d be spending there. I went back to my flat to grab my scope. The terrace’s view more eastward than my flat and this allowed me to see the first eight courtyards of the prison in angles closer to each other. Almost 10 minutes after I set up the scope, there was a group of people at the corner of the second courtyard and they had started to chat. I participated their conversation with my scope and tried to lip read but I wasn’t able to distinguish words. The eastward terrace brought the sun right in front of me at this hour and the sun illuminated the terrace all along. I didn’t think that the sun would directly hit the lens. The areas I was focusing were absorbing and reflecting the light and they became blurry from time to time. I kept recording for some time, and took a time off of the scope and leaned on the railings of the terrace to watch around. This place was exposed to wind; it would be surely cold when there was no sun. When I checked the weather from my phone, I saw that cloudy days were about to start. Because I would be on the terrace for the rest of the day, I started thinking about the set up there. At 2:00 p.m., I focused on "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0952"}},[_vm._v("the second courtyard")]),_vm._v(" again.In the group, the bulkier one was sitting on the edge, he was doing the talking mostly and four others around the table were listening to him. After a long monologue, the others started to participate as well. The bulkier one came face to face with the long, black haired person I had seen the first day and started talking to him. As the chat got longer, it was understood that the long-haired one got bored. After 1-2 minutes, he raised slowly from his chair and started touring the courtyard. I couldn’t remember seeing before the two people, on young and the old, who were sitting at the table. The young one was wearing a dark colored sweater with figures on it, the older one was wearing a similar attire but without figures. The long-haired inmate said something to the bulky inmate while touring and then, entered through the courtyard door. Three people left at the table kept chatting. Young one was only listening and the oldest at the table maintained the conversation by saying something now and then. For a minute, I heard a noise coming from the stair well, I turned back and went to the terrace door to check the noise and stumbled upon the janitor cleaning. She didn’t come into the terrace and asked me what I was doing. I said I was living there and taking photos of the surroundings and I chose to come here because the shared terraces were locked. She said that large terraces had been open before but the patrons were taking out their animals to toilet there, so the terrace doors had been bolted. Thus, I learned the story behind the bolts I saw while examining the terraces. Our conversation didn’t last even a minute, the janitor didn’t feel anxious about my presence on the terrace and went downstairs and I went back to the terrace. Suddenly the activity in the courtyards increased, it looked like a mid-day break. In the seventh courtyard two people were walking next to each other along the yard, they both were wearing black sweaters and jeans, their "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1282"}},[_vm._v("black shoes")]),_vm._v(" looked very much alike. They conversed while walking in the same tempo, they might be close friends. In the fifth courtyard, three people around a table were drinking tea and talking about an object one of them was holding, but I didn’t get what it was exactly. It was dark and thus, its contours were not clear, I squinted my eye to understand what it was as they did. In the third courtyard, there were two empty chairs with floral-patterned cushions on them. No activity was around them; they were aligned back to back. I think it’s about the hours of permission that the activity increased before 3:00 p.m. After 3, it became less and less intense. I was hungry, I decided to go back to my flat to have some food. While I was trying to open the door of my flat, key holder was stuck to the doorknob and broke free from its chain. I saw that one end of the key holder changed shape and I tried to straighten it by applying pressure with my hands. It’s 3:20 p.m., after bringing the key holder to its original shape, I warmed the frozen pizza I took out from the fridge, I ate it and quickly returned to the terrace. After I arrived at the terrace, a military vehicle entered the premises, the driver and some other person in uniform got off the vehicle, they wandered around a bit in the garden and then went inside. At 3:50, two visitors came inside – mostly the visitors were wearing color black. At 5:00, one of the visitors left 2-3 minutes after entering. The time spent by visitors inside varies. The number of patrolling guards increased to 1 at 5:15. I leaned the patrolling schedule by heart. I start writing this part at 11:00 p.m. During my time on terrace, I had recorded sound mostly. Sun started setting at 5:30 while I was on terrace and got darker slowly. After the sunset, there was no activity for an hour. Because the weather was cold, I was periodically going to the platform connected to the stairwell to get warm, and then returning to the terrace. I didn’t notice any activity in the courtyards but suddenly I realized three people in the third courtyard, one of them was holding a ball in his hand. Three people around the yellow ball had orange, lack and brown clothes which, as it got darker, turned into volumes wandering around the courtyard. As they were playing ball, their actions in the courtyard encountered and coincided these volumes to each other. The one with "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1014"}},[_vm._v("orange t-shirt")]),_vm._v(" , controlled the ball more than the other two, and while the other two were resting, he kept playing the ball by bouncing it between himself and the wall. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1081"}},[_vm._v("The clothes")]),_vm._v(" hung on the long rope between the two opposing walls of the courtyard defined the goal posts. Since they were three people, the one with brown t-shirt was changing teams occasionally to even the game. 20 minutes after the game started, the ward door was opened and while the janitor was picking up the trash from the courtyard, black uniformed guard came to the courtyard. The inmates in the courtyard stopped playing and started preparing. After everyone went into their wards, the guard started to take a last tour in the yard. He came closer to a wall and examined it in detail and then, closed the ward door and went back inside. Open spaces were calm again, I was following the lights turning on and off again on the windows lining up along the building, sometimes I was able to see shadows when there was a light on but they were disappearing in no time. Towards the night, it made me alive to be in an open-air space on the terrace. It supports the idea that the terrace was isolated that I had only seen one janitor throughout the day. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1005"}},[_vm._v("Until 10:00 p.m")]),_vm._v(" I spent time on the terrace and focused on the moon with my scope, I was able to see the indentations and protrusions of the moon craters to some extent with this mid-range scope. Just to make sure that terrace was open until the late hours of the night, I stayed there a bit more. Nobody was in any courtyard. There were only two people patrolling. This night, different than the others, no car arrived for transfer. Until going back to the flat, I kept watching the sky with my scope. I returned to the flat at 10:45 p.m. I ate the rest of the pizza I had warmed. Because I left the apartment windows closed, there was a sweltering heat at the flat. The ideal temperature in these flats with central heating is too high for me. Before, I was spending time by keeping the windows open. I took a look at the prison from my flat once more; the highway filling the gap between graphically and audially was shining with the traffic. It was a road swerving along the tranquility and silence of my flat and the courtyards, blurring frequently due to its rhythm and pace. In this building and the prison, outside the "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_1006"}},[_vm._v("rhythm")]),_vm._v(" that surrounds us, we were sleeping and waking up, and taking fresh air around the same time. It’s 4:00 a.m., I had exercised for an hour after my last entry. I felt my body was rusted while watching inactive. All the night, I didn’t see anybody except the guards. The patrol was always at the same point in periods of 45-50 minutes. While resting and watching around, I think that a sleep of 1 hour would be enough to rally up. ")],1),_c('AmpersandSeparator'),_c('div',{attrs:{"id":"section-3"}}),_c('span',[_vm._v(" I slept for a short period of time, from 5:00 to 6:00 a.m. When I woke up, it was silent, I got honey and butter from the kitchen and went next to the window. Almost every morning I ate honey, but this was the first time I got to eat honey since I started staying here. The weather was in accordance with the report I saw the day before. While planning for an early terrace excursion, the cloudy weather for all day long was a sign that the sun wouldn’t shine on lenses. I am not sure whether the vigor I felt was due to the period of time I had slept in. It might well be the false transient effect of the motivation of the bees which make honey by working constantly and regularly. At 9:05 a.m., I was on terrace, while I was setting up the tripods for my scope, 3 cars entered. A military shuttle, a service shuttle and an ambulance were all together. A guard in black uniforms got out of the blue military shuttle that was closest to the entrance, the guard who came to greet him from the building was also wearing the same uniform. The number of blue-wearing guards were increasing in order to accompany the transfer process. I have started to distinguish the colors of the uniforms according to the tasks and duties. Black uniform officers were maintaining the control over the space and responsible for the blue uniform officers, they were less in numbers. Mostly, they gave orders during transfers, blue uniform guards were following them. The guards gathered around the cars talked among themselves until 9:15. The uniforms in two colors were intertwined and made it hard for me to distinguish the guards. At 9:20 a.m., 4 guards came out with an inmate wearing a black jumper and green trousers, the handcuffs he had shone on my lens among the crowd, he might be 25 years old. Generally the inmates get on a military shuttle, but this time he was taken to a "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1237"}},[_vm._v("white")]),_vm._v(" shuttle. The windows of the shuttle were filmed and covered with thin metal mesh, I was only able to see what’s inside from its back door. No one else was carried to the ambulance or to the military vehicle, blue uniform officers got on to the two empty vehicles and then, the cars exited. Some of these vehicles are used to transport the guards within the prison. At 9:45 a.m. a pickup truck came closer to the entrance, normally I was expecting a garbage truck at this time of the day, I hadn’t seen a white truck coming in the days before. The vehicle came very close to the transfer area but still I was able to see the gate where transfer process was handled. I focused on that gap and waited, the color turquoise came in contrast to the fabric surrounding it from this gap left under the shadows. Those were the turquoise jars of human height, when I zoomed in and focused, I realized these volumes were plastic water jugs. Aligned over and next to each other, these plastic jugs were carried wrapped together. There were so many packages being carried that for a minute, I imagined the number of the people inside and the scale of the prison. These packages reminded me so simply a scale that had slipped my mind through "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1213"}},[_vm._v("numbers and dimensions")]),_vm._v(". During the transportation, the water in each jug was responding to the changing balance at the same time and transformed into a large wave inside the package. I watched the transportation for a long time. While I was focused on the packages, it started to pour down, between my focus and me was a blurry and vaporous texture filtered slightly by the rain. This texture, actually, relieved me, it was a relief about me becoming less visible. Due to the hard rain, I started wrapping the scope with a plastic bag I found on the terrace. While wrapping the plastic bag, the water flowing under my feet moved towards the terrace’s drain. I saw the increased number of puddles of water on the courtyards when I looked with my scope. The sky view reflected on the puddle was getting larger and large at the center of the courtyard. The water was expanding so fast that I was not able to see the concrete courtyard filling my view frame because of the reflection. Simultaneously, I was able to surround the lens with the plastic bag. When I turned my scope to the entrance, the white pickup truck that had brought the water packages wasn’t there, and I saw the dry floor on which the white pickup trucked had waited and "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1225"}},[_vm._v("protected")]),_vm._v(" it from the rain. At 10:25 a.m. the garbage truck came almost one hour late than usual. A similar delay was observed in preparing and aligning the trash outside. After loading the trash quickly, the truck left the premises. The flaws in the rhythm of the space started to attract my attention more and more as I keep watching longer. I didn’t see anyone in the courtyards so I focused on the car entrance again. At 10:45, the shuttle I had seen earlier entered again. The youngest of the three got off the car was holding his gun with his both hands and waiting next to the backdoor of the vehicle. Because of the way he handled his gun, I guess, another guard talked to the young guard frowning and showing his hand. I guessed that he had a high-rank from the plethora of graphics he had on his arm. Young guard hung down the gun to his waist and after the third guardian got off the car, they went to the building. The position of my hands holding the scope in order to aim on the target, follow the image and focus, looked like the way the young guardian was holding the gun and warned about; while my hand behind the scope was arranging the focus and the frame, the hand in front was "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1245"}},[_vm._v("gripping the lens")]),_vm._v(" in order to dampen the vibrations. Service shuttle left after dropping the guards. At 10:55 a.m. I saw two visitors, they walked fast to the entrance door with files on their hands, these could be the youngest among the visitors I had seen up to that point. In order to capture their time between the outer and inner doors, I fixed my viewfinder by guessing their next move. Knowing the routes and the short cuts the patrols, the transfers and the visitors used frequently in their circulations had enabled me to prepare the viewfinder and record the situation. In time, the guessing game I had played at the beginning transformed into a plan in which I prepared my viewfinder before. If I trusted so much in these routes, I might have missed people from "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1266"}},[_vm._v("my focus")]),_vm._v(" easily. At 11:00 a.m. the rain stopped and the courtyard doors started to open. Two friends in the eighth courtyard started walking the floor while chatting in their regular rhythm, another inmate who accompanied them this time adjusted to their rhythm in five minutes. In the third courtyard, the inmate wearing an orange t-shirt of those played ball the evening before, was sweeping the puddles of water on the floor to the drain of the courtyard. The water gathered by the drains on the terrace left dark gray marks under my feet. The plastic chairs were brought out, and at that moment, I understood that not only the courtyard but also the ward inside was going through a cleaning. The foamy water swept from the ward into the courtyard gathered around the drains. As it moved towards the outer parts of the courtyard, the white foam was getting darker and disappearing as a dark gray cloud when reaching to the drain, they were no different than the clouds that were ready to rain just before. Two other inmates played ball the day before, started to take out the plastic chairs from the ward after taking over the brooms. The chairs aligned on a single line to the center of the courtyard kept stretching out as they leant on each other’s back. I thought I "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1345"}},[_vm._v("remembered")]),_vm._v(" some of the cushions, pieces of cardboards and scratches they were carrying on them. At 11:30 a.m., the sixth courtyard attracted my attention. I remembered that this courtyard that I fixed my focus on was different than the others. During my time here, my attention moved from one center to the other in pieces thus, I forgot about the immobile areas. There were a stone structure built in parallel to the back wall of this courtyard, some flower pots located around it, and a tilted table. I hadn’t focused on any section around the highway and subway which were the only views I had with a "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0968"}},[_vm._v("bank")]),_vm._v(" located almost at the center of the courtyard. The pedestrians surrounding this section at stops were substituted by stuff pushed to the sides at the courtyard. It looked like an open-air warehouse. The first time I was attracted to this courtyard was a man who entered the courtyard, wearing jeans and a coat. It was the puffiness that informed me that he was dressed in layers. During his time taking a stroll, he didn’t follow a linear route between two ends of the courtyard, he followed a circular route at the center without coming closer to the walls. During his time at the courtyard, his hands were in his pockets, my hands, on the other hand, was shaking my focus with the increasingly cold air. At 11:55 a.m., the number of people in the courtyards decreased, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1306"}},[_vm._v("in the first courtyard")]),_vm._v(" brown-sweater inmate who always kept his rhythm was having his last tours before going back in. After he entered, he came back to the courtyard with water bottles of 5 liters. He put bottles against the concrete flower pot at the center and went to bring 4 more bottles and put them next to each other. It was a method where he used the coldness of the air – I don’t like lukewarm water. After carrying the bottles, he sat on a chair at the corner of the courtyard to relax. While having a rest, he chatted with another inmate with black jumper and beanie who came to the courtyard and later, they both went inside. For about 20 minutes, I didn’t see anyone, I started to feel the harsh breeze more and more. From time to time I warmed myself by entering the closed landing of the stair well. The silence kept on for another 30 minutes. Because of the continuous process I had on the terrace I didn’t think of going to the toilet, I peed at a far corner of the terrace. Then, I remembered how the janitor had told me about the reason why the other terraces had been closed. They were bolted because the patrons were taking out their pets to toilet there. I smiled when I remembered. At 13:05 a couple of people were seen in the courtyards, I thought that the one-hour break was about the lunch. The first activity caught my eye in no time. There was someone running in the eighth courtyard. His rhythm was faster than anyone having a stroll up to that point, his breathing made me think that it was going to be a disciplined and long run. For 10 minutes, I zoomed in his ceaseless touring, he was walking to the end of the wall and returning swiftly to complete the tour and increasing his pace without lowering his hands from his chest. I remembered the inmate playing ball with the wall the other evening. Both, in a "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_1326"}},[_vm._v("repetition")]),_vm._v(" balancing and strengthening their own bodies, were working unconditionally for the unknown outside. I realized that the runner was tired and out of breath after a run of 20 minutes total from the frequent breath vapor he was exhaling. He went straight back inside not to spend time in the cold weather. At 1:25 p.m., in the fifth courtyard, the glasses left on the table in the morning were collected by two people and brought back full. Later, two other inmates joined the table, it was easily understood that there was an age-gap between them. Young men pulling the chairs towards the table started to sit and chat with the others. Those two, who had been sitting before, had striped beanies, and were dressed in layers, the young ones had t-shirts and tracksuits. After a short conversation, two inmates wearing the striped beanies started to walk, the others stayed and kept chatting. While their conversation continued, I saw two people walking next to each other in the second courtyard, when I focused on them, I noticed that one of them had a bandage on his head and was getting a leg up from the other inmate. While walking around the courtyard at same pace, they were talking, they were almost the same age. Every time they got closer to the wall, the inmate with a bandage on his head turned around himself and clamped his arms to the one next to him to get support. The wind was blowing harder, my scope was shaking more than ever, in the meantime, people in the courtyards started to go back inside inside my swashing focus. If this wind keeps going for a long time, I might get sick. A single chair left in the wind in the third courtyard fell and was dragged by the wind. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1380"}},[_vm._v("The fallen chair")]),_vm._v(" was the last activity in the courtyards around 1:40. I was guessing that the courtyards would stay calm. Mostly, they are empty between 2:00-4:00 p.m. After forty minutes, I got hungry. I had left a slice of pizza on the counter. I went back quickly to my flat, had the pizza and came back to the terrace, it lasted for a total of 15 minutes, when I came back, I didn’t see anyone. It was good to get warm during my time back at the flat and I brought extra water for me. Until 4:10 p.m., there had been only one visitor, other than that, the number of guards in each patrol had been two. Among the quietness, I zoomed in to the construction sites dispersed as islands around the overpass just in front of me. When there was no construction, there would be 2 or 3 people spending time on those piece of lands surrounded by the roads. Inside, there are partly raw and partly processed material and pits dug in order to install the scaffoldings. When looking from above at the pits created by the half-construction, I thought they were the darkest traces I would be able to see around. Even though I had a position overlooking the whole landscape, these black spots might be my blind spots. They were no different than the black surface surrounding the flat in my recording view frame. After 4:10 p.m. the number of people in the courtyards started to increase gradually. A group of four in the fifth courtyard gathered together, they took the same order they had been in the day before and started talking. At the same time, a large group started to gather around a table on the edge of the second courtyard. I can say that I know almost all of them, I see only two of them for the first time. The inmate wearing a football jersey and the young inmate wearing a gray jumpsuit were added to the group and they were now eight. The red-jumper one at the corner was talking frequently and the others were listening to him. His friend whom he had talked one on one two days before was there too. Because of the number of people involved this time, the number of dialogues within the group increased also. The new one with the jersey was a talker, he was mostly in dialogue with the young ones. I was able to guess who’s talking to who via the bodies stretched to each other’s. I started to care more for the expressions of the people talking. The one sitting on the right side of the table was the youngest among them and talking the least. When he talked, he just did it quickly using his hands much, he had a black coat and inside wearing a gray turtleneck jumper. That he was talking less and listens more separated him from the general rhythm of the table. The ninth person bringing the teapot to the table was a middle aged man, the red coat he was wearing was similar to the other red coats I had seen. He had prepared the paper glasses beforehand. The young one served the tea quickly to people around him, and gave it to the other side of the table. One after another, all lit up a cigarette. Seven people of nine were smoking, all of them fitted into my frame hardly, if another one were to be joined, he would have stayed outside of the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1394"}},[_vm._v("circle")]),_vm._v(". Around 4:30 p.m, when I took a step away from the chat in the second courtyard, I noticed an activity in the fourth courtyard which I resembled to a warehouse, someone with a black puffy coat turned the table upside in the courtyard and rested it against the wall, and there was a big gap in the middle. After the table turned upside down, the stuff pushed aside were standing in vertical and transverse angles, different than their regular usage, and transformed the center of the courtyard into a square. My lenses fixed at the newly opened gap; it made me feel good to look from my window frame at this gap at the end of an image after passing through the walls surrounded by the courtyard and the prison surrounded by the highway. After a long watch, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1389"}},[_vm._v("3 people")]),_vm._v(" entered the first courtyard, I recognized the brown-sweater one who had been walking regularly before. The other one I didn’t recognize was wearing a cream color hoodie, I was guessing that he was about 25-30. They both were drinking tea, the one with navy blue jumper must have told a funny story, they were laughing from time to time. During their conversation, the brown-sweater one entered the courtyard for his daily walk, after greeting the people he started touring around the concrete flowerpot in the middle. During the walk that lasted until 4:50, he participated in the dialogue at the table from time to time and after he finished touring, he went back inside with the others. In the second courtyard, the crowd around the table also disbanded. I focused on one of the watch towers as the courtyards were calm, the increasing breeze started to shake my focus again. I saw the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3750"}},[_vm._v("torso")]),_vm._v(" of the guard waiting in the tower, because of the angle I was looking through, the roof of the tower was covering his face and exposing only his torso. I don’t know if he was one of the guards I saw patrol. The wind is getting stronger, the coke can I realized with the wind on the terrace started to make noise bouncing from the walls, its cylindrical body was rolling from one side to the other side of the terrace. I stepped on the can to flatten it, it stopped to make noise on the very spot without moving. It’s about 5:20 p.m., two friends in the seventh courtyard were strolling while chatting as always, an inmate went into the empty second courtyard. The inmate in the courtyard was the young man wearing black coat and gray turtleneck sweater. He was walking with quicksteps, he was attentive and expressionless. He walked towards the wall with his hands in his pockets and returned swiftly back, he was coming so close to the wall that while turning his shoulder was touching the wall and he was sweeping "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_1417"}},[_vm._v("the whole yard")]),_vm._v(" completely. He kept walking for 20 minutes and went back inside. At 5:50, nobody was there in the courtyards, as it got darker, it could be seen from the clouds that the rain would start. From the ambulance and the service shuttle entered through a slowly-opening car entrance door, the inmate I knew from the first courtyard got off. He was accompanied in by two guards coming from the patrol route, the road was short because he was in the ward connected to the first courtyard, it was the closest to the entrance. At 6:10, while it was getting darker and colder, the prison lights were turned on, the entrance and the courtyards were illuminated, it was only the patrol route where the lights were not turned on, mostly the lights were on there. Just after, the lights on the other half of the terrace were opened. The patrol route and I were under the shadow. Then, the lights of the ward started to turn on and the shadows from the windows became distinct. The doors of the courtyards might start to close in half an hour as the days before. Almost 20 minutes later, those who played ball in the third courtyard went outside, they had a ball. Their game lasted for only ten minutes and after talking to someone through the window, they moved towards the door. When they got close to the door, janitor got to the courtyard, he had a wheeled orange trash can he was pulling. After the first trash can he leaned on the wall, he moved towards the others waiting inside. He carried one by one the trash cans, some of which had a chemical waste sign. After taking out some trash from the cans, he started to pour the liquid gathered in the trash bag into the drain at "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1591"}},[_vm._v("the center")]),_vm._v(" of the courtyard. He repeated this for some time and aligned the cans along the wall. In every can, there were different colored plastic bags. After counting the bags, he took the cans inside. He might be preparing for the day after’s garbage collection. After the janitor went inside, the black-uniform guard came to the courtyard and watched the sky for short time. At 6:40 p.m., the prison wrapped itself up in a long silence and on the contrary, the highway between us, it was filling up with the crowds of the rush hour. I started to feel the physical effects of my ninth hour on the terrace on my body. There were snowflakes among the raindrops. This was the first snow of the year that I have witnessed. After waiting for some more, I went to the flat, "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_1592"}},[_vm._v("I was starving.")]),_vm._v(" The first thing I did when I was back on the flat was to sit next to the radiator. The first symptom of feeling cold on the terrace was the shaking focus and only now that I was next to the radiator I realized how much I was exposed to the cold. When my shaking decreased, I started to hear the blowing noises that the wind and storm hitting the windows of the flat made. The toughest and coldest storm was that night, it toyed with the resolution of my regular view and made it harder for me to focus. I didn’t see anybody in the prison; the courtyards, the garden, the patrol routes were all empty, we both went into hibernation in our havens. At 7:00 p.m. I opened the door of the freezer, I totally forgot that I had to defrost the frozen meat before. To be able to cook eat fast, I started to defrost it by running hot water on it. After the blood from the defrosting meat poured to the sink, I separated by my hand, a piece of it that I was going to have. The other pieces were defrosted too, so I thought, they might spoil if I tried to froze them again. I was frequently hearing the dogs wandering around in the construction site below me. I connected the meat together by a rope and threw them into the construction site. The meat I was watching fall as I was leaning on the window, transformed into a scarlet-brown dot and hit the ground. I heard its noise from the height I was. Staying on the terrace for long made me hungry. I started making pasta for a side dish to meat. While they were cooking, I checked whether I can see someone by coming closer to the window. At 8:00 p.m. I had already finished eating and I was full. The weather was slightly warmer but still cold. I was barely able to see the guards patrolling on their route which was not illuminated, they were there again on their usual time. It’s 2:00 a.m., I haven’t seen "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1151"}},[_vm._v("anyone else")]),_vm._v(" other than the patrolling guards all night. It’s 3:30, I was hearing the barking of the dogs and the speeding of the cars on the empty highway, since I was lying on the couch. ")],1),_c('AmpersandSeparator'),_c('div',{attrs:{"id":"section-4"}}),_c('span',[_vm._v(" Having accompanied the the inactivity for all night long, I submitted to sleep at 4:00 a.m. and slept for four hours. I woke up at 8 on the couch with the sunlight. I drowsed off without taking any notes at night, I found the pen between the cushion and the couch. The only thing I have seen before sleeping were the guards showing themselves under the light fallen on the edge of the patrol route every hour. This spotlight located on the edge where the patrol route changes direction provides illumination for a circular area but does not lighten up the major part of the route. Around 3:30 a.m., the route submitted to the darkness when the lights over the patrol route were turned off. I wasn’t able to see the next patrol of the guards regardless of how long I moved my lens around. It was expected that in prisons, the area overseen per guard was big but when the lights went off, the range of vision was so limited for the watchtowers, guards in patrol and me. The façade lights were turned off in my residence at 3:50, the same darkness should have applied for those looking at my direction. At 9:30 a.m., the circulation started with the entrance of three employees, they had plastic bags in their hands as always. The traffic stretching from the highway in front of me to the overpass was increasing its intensity as it got farther away from me and the horns blowing were barely heard. I listened the horns right after the employees entered from the front door. At 10:10, the garbage truck was on time, the garbage was taken out just before, some of them were carried in plastic bags while others were in garbage cans. For the first time I saw the green garbage cans next to the orange once I had seen the night before. While the garbage truck was leaving the premises, the visitors coming attracted my attention, normally I didn’t encounter any visitors at this time of the day. This time, two people directly head towards the front yard and walked around with documents in their hand, talking on the phone. Later, two more people entered with 20 minutes apart from the front door. The number of the people in the front yard was considerable with the visitors having entered one after the other. I can say a normal frequency on a day is 1-2 people at a time. At 11:10, the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0492"}},[_vm._v("patrol")]),_vm._v(" followed the usual route, but this time there was only one guard doing it. Until 11:30, the tranquility ruled in the courtyards, some visitors exited, besides, the transfer door was opened, an employee from the inside left the stretcher he was holding at a corner next to the outer door of the building. I was thinking that they were preparing for a vehicle to be arrived. In 5 minutes came an ambulance. Someone with a red beanie was taken out on a stretcher. This was the inmate I know from the first courtyard, I remembered their conversation before with the inmate taking care of the flowers in the courtyard. The stretcher on the side was not touched yet. At noon, the stretcher came inside empty, maybe it was a spare one for the coming ambulance. I didn’t get to the terrace early this time because I didn’t see anyone on the terraces. When the transfer door was closed, I started to get hungry with the calmness around, I cooked some eggs and kept looking through my scope. While I was not waiting to see anyone, a minibus appeared at the outer door all of a sudden, I had seen one of the guards coming out often, I only realized him after I focused him with my lens since he was wearing a hoodie. The puddles that the rain the other day had created were all along the courtyard and after the storm, it was still cold, I thought that must be the reason that the inmate I knew was wearing a hoodie. Four people left the car, the last one was a high-ranking guard, he walked hastily towards the front and hold the hood of the young one and pulled it. No other guard was wearing a "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1807"}},[_vm._v("hood")]),_vm._v(". After the hood was taken off, he said something in an angry manner, his mouth was moving all the time. After talking a bit more, he stopped holding the hood, the young guard headed towards the patrol route between inner and outer walls, without saying anything, and the rest followed him. The high ranking guard took some documents from the front seat of the car and went inside. The smell reaching my nose made me realize that the eggs were burned. Since I was following what was happening around the car ceaselessly, I returned only to see a pitch black pan. Thinking that I could witness another moment, I left the pan in the sink and returned to the window. At 1:00 p.m., the patrol guards from the previous shuttle gathered at the same ground when another military vehicle entered the premises, the young guard was among them again. Right after them, the entrance door was opened and the shuttle entered. The high ranking guard coming out of the transfer door started to walk through the car. Blue uniform guards were getting out of the newly arrived shuttle, someone got off the front seat and went next to the young guard and started to joke around. It was obvious that they knew each other, they got a bit far from the shuttle and started chatting. During the 10-minute chat, an inmate wearing a white sweater came out with the guards. A middle aged inmate with stubbly beard was spotted easily with his white sweater among the blue uniforms. After being accompanied to the shuttle, the doors were closed. Of the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1811"}},[_vm._v("guards talking")]),_vm._v(" aside, the one come with the shuttle, said goodbye to his friend and got in the car. When everyone was heading back to their position, I forgot my silence at this flat even after all those dialogues I had witnessed. While focusing on the dialogues graphically, I was not feeling outside the conversation or silent. The notes I’m writing in and reading from my diary are parts of a similar exercise. I am sure that my neighbors didn’t even hear a rattle. At 1:20 p.m., after the busyness on the transfer area, the front yard and the courtyards started to liven up at the same time. The brown-sweater inmate walking in the first courtyard checked the plant at the center. Two friends were conversing in their daily routine in the seventh courtyard. Two people with colorful beanies in the fifth courtyard were walking around the courtyard in the same pace. Although each courtyard was divided by the walls, the uniformity of the areas they were on brought closer, or sometimes intersected perfectly, the inmates’ walking directions and paces. It was possible to see only from this distance their resemblance like soldiers marching in the courtyards aligned next to each other. The young inmate with gray jumpsuit who entered the fifth courtyard later, arranged the chairs around and went inside, two inmates with beanies walking arranged the chairs at the other end of the courtyard and kept walking. In the front yard, the visitors were still waiting, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1846"}},[_vm._v("the visitor")]),_vm._v(" who was talking for 20 minutes after having entered toured the front yard for 7-8 times all along and was keep doing it. When the visitor came close to the garden wall, I realized the mural he turned his back to. The wall colored in blue had the same color with the visitor’s sweater. When I zoomed in on the wall, I saw a horizontal line dividing the mural right at its center. This line divides the mural into two equal parts as sea and sky. Black and white patterned fish swimming under the sea and colorful birds flying in the sky, were all scattered to the upper and lower parts of the wall as the creatures of these two media. This mural covering the most of the wall had been peeled off and discolored in time, I thought that it had been painted when the prison was also new. The other people I had seen in the front yard were employees working inside, they were hanging out in the garden while talking on the phone. I was able to tell that they were employees from their slippers and uniforms, the ones with green aprons were most probably healthcare professionals, the last time I had seen the aprons of the same "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1843"}},[_vm._v("color")]),_vm._v(" was when I had been in the hospital. At 1:40 p.m., the service shuttle came to the entrance door for the third time, the inmate wearing the football jersey that I knew from the second courtyard was taken to the shuttle. The guard accompanying him took the front seat with documents in his hand, the two remaining guards talked to someone in green uniform who got out of the building with a file in his hand. The employee in green uniform had gloves and a stethoscope around his neck and was checking something on a piece of paper he was holding. After a short dialogue, two guards got on to the backseat of the shuttle and the shuttle went away. After a short time, I saw a black uniform guard gathering blackberries in the front yard. On the greenery surrounding a small part of the garden, there were many blackberries. The guardian went into the bushes to gather them in his palm and started eating them one by one. He walked around the blackberries for 10-15 minutes. I took "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1899"}},[_vm._v("the apple and tangerine")]),_vm._v(" I found in the fridge when I remembered that my breakfast was burned and kept watching while eating them. At 1:55 p.m., someone got out of the building in plain clothes and went next to the statue in the front yard. He came closer to the statue and started to examine it, then I focus on the statue and read what was written on it: “it is enough to follow what the civilization orders and requires to be human.” This statue standing on the front yard where the building entrance was also located was in good condition, it was obvious that the climate did not affect it so much. As I was thinking that it was kept clean all the time, the man examining the statue came back with a lid of a paint can and a spatula in his hand. On the lid, there was white plaster. After scraping off of the lid, he started to apply it on the side of the statue. For about 20 minutes I watch the statue "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1946"}},[_vm._v("renewed")]),_vm._v(", the cleanliness of this statue, which had layers and layers of make-up on it, distinguished itself on a surreal manner from the surrounding old buildings and objects. It was a quote that was not allowed to get old among all the things rotting and getting old. After the plastering ended, I centered my attention back at the kitchen, I scratched the burned and stuck egg off the pan. After cleaning the egg, I went to the terrace, but this time I took extra clothes and fruits with me. At 2:30 p.m., the front yard was less crowded and I focused on the courtyards. Compared to the day before, it was calm. Only two people were sitting at one edge of the second courtyard, the one who did the most of the talking in their chat around the table and his long-haired mustached friend were talking. The table was carried from one end to the other of the courtyard –since the corner of the courtyard had received sun the day before, it might have been carried to that corner. Where they were at right now was the farthest place to the ward door on the wall they were leaning on. While they were conversing, the door of the farthest watch tower caught my eye, it stayed open for a while and the blue-uniformed "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1921"}},[_vm._v("guard")]),_vm._v(" came out and walked towards the intersection of the tower and the wall – there might be a long way between the watchtowers and the toilet- he started peeing on corner under the watch tower, later he closed the door and went back to his position on the tower. At 3:10 p.m., the chairs gathered together again in the third courtyard. This time, less chairs were positioned in a circle, some had clothes on them. There was no cleaning going on, at least, on where I saw. The inmate touring around the chairs was the one I remembered from the previous days playing ball, wearing a dark green vest. While wandering in the courtyard he was looking up frequently, from his point of view, only the residencies I was in and the skyscrapers far away could have been seen. For the courtyard with a view of only the sky, the most up-to-date information could be from the reflecting glass cover of this residence. Limited by the height of the walls, the area of information became the building I was in and in turn, my area of information is these courtyards. That he was constantly looking up reminded me of astronauts. Of course, I was questioning all the time whether someone looking up could see me, yet it required a strong and constant attention to spot a small, immobile dot in a large texture. It’s 3:45 p.m., the service shuttle entered for the fourth time. Today, the circulation of this vehicle was extraordinary. I thought that I might have encountered a control or a medical examination day. Two inmates that I hadn’t seen before were taken to the vehicle. The inmate taken out was the one who got in at around 1:40 p.m., the one wearing a football jersey, maybe he was coming back from a medical examination. The guard I saw behind the transfer door, standing under the shadow, was called to the vehicle, sliding backdoor of the security vehicle was opened forcefully by a key. After the inmate got in to the car, shuttle headed towards the outer entrance, while the door was opening two visitors were walking towards the visitor exit, the car and the visitors left the premises simultaneously. At around 4:00, I moved my focus to the courtyards, I knew I would start seeing people. I saw the first group in the first courtyard, there were 3 young people together with the inmate transferred on a stretcher early in the morning. I had the opportunity to follow the conversation of these 3 young inmates, they were generally together when they were in the courtyard, it might well be because they were almost the same age. This time the inmate on a stretcher joined them, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1975"}},[_vm._v("their conversation lasted long")]),_vm._v(". In the group, mostly, the inmate who didn’t chose to sit and kept standing up and the one on the stretcher did the talking, two other young inmates wearing both black jumpers were listening to them. The chairs were positioned in a circle and the table was taken closer to the wall. I could say that their chat lasted for forty minutes. While they were talking, the brown-sweater inmate who never skipped his daily exercise, started touring the courtyard in his regular pace, and didn’t join the conversation so much. At 4:55 p.m., the friends walking together in the seventh courtyard had pulled a table to the center of the courtyard and continued their dialogue this time while drinking tea, I hadn’t seen them converse around the table for the last four days. Through the evening, the glasses and cans of drinks increased on the table. At around 5:10, the inmate with long hair and moustache was walking together with someone wearing a cream jumper. This inmate was a bit over middle-age and short, he was doing the talking most of the time, and the mustached was listening. It was the first time I have seen that the mustached one was touring the courtyard, when I focused on his walk, I noticed that one of his shoulders was before the other, maybe he positioned himself that way for the dialogue. They went back inside after a chat of 20 minutes. The inmate mostly took to the courtyard to run in the eighth one was not seen today. Around 6:00 p.m., I focused on the patrol route, this time groups of two guards from opposing directions were seen, it might be the shift time for watch towers. While walking facing each other, they made faces and greeted each other and kept walking to opposing ends. At 6:20, it became calm like the other days, the lights of the residence and the prison started to turn on. But this time I didn’t see anyone playing ball in the third courtyard. The highway between us was the most intense because it was almost the rush hour. The roads along the view were various and innumerous, that allowed me to witness many faults and flaws going on in them. My attention to the extraordinary situations on the road decreased as I happened to see many troubles on the roads. I moved around my focus between the cars with 4-way flashers open. It was quite possible to come across more than two due to the number of roads I could see. These stranded cars were accompanying to the calmness and silence of the prison and the flat I was in. That they were stopping not only separated them from the rhythm around but also managed to suspend the traffic. Some drivers were thinking alternatives to avoid the traffic, while "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_0657"}},[_vm._v("the yellow lights")]),_vm._v(" turned on to fix the troubles were blinking like ambulances and police cars bearing the signs of emergence – a warning light having the worry for constant immobility like us. The driver, most probably, tried his best to fix the problem and go back into the flow again. At 6:50 p.m., two employees went out from the main entrance with plastic bags in their hands, because the plastic bags were transparent, the green color of the uniforms inside were seen. While they were passing through the door, they coincided with the patrolling guards, this time some bulky guard I had not seen before was joined to the patrolling group of two guards. This short period of crowdedness lent itself to a long period of calmness, after the horns from the highway I moved my attention back to the road again. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2045"}},[_vm._v("The chair")]),_vm._v(" standing at the center of one of the construction sites was toppled over due to the wind, it was the same chair as the plastic white chair that had toppled over in the third courtyard. The security walking around the construction material surrounding the whole environment did not realize that the chair was toppled over. After slowly examining the material, he returned to the security cabin. It’s 7:35 p.m., I didn’t get cold staying on terrace more since it was not cold as the evening before, the snacks I brought with me kept me full. I sat on the wide gap between the railings of the terrace and looked down. Now that was completely dark I had no fear of being seen, just as the patrol routes, there was the shadow of the roof over me. During my time sitting there, my field of vision had expanded in every direction, I was able to see the farther boundaries of the prison and the northern end of the highway. The courtyards furthest away from me looked like dimensionless graphics when looked at by naked eye. I saw objects hung on the wires above the walls that every courtyard connected to. My eyes regarded them as some volumes, I jumped back on the terrace and leaned my scope to the edge of the terrace and arranged the focus. As I zoomed in, I was able to see some objects wrapped in plastic bags, since the bags were opaque, it became harder for me to guess what’s inside, in addition to these plastic bags, I could see balls hanging from the wires. Before I had read that a way of connection between the courtyards was throwing notes by wrapping them into something, maybe these were a series of notes that were unsuccessful. The calmness continued, so I gathered my stuff and headed "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0979"}},[_vm._v("towards the flat")]),_vm._v(". It was 8:37 p.m., on the window of the guard tower connected to the car and visitor entrance, the lights were even brighter than the usual. Normally, the guard in the tower was responsible for checking the entrances and exits, the car entrance gate and the documents. For only this night, the light of that tower caught my eye more than normal. Due to the density of fences between us, it was generally hard for me to see that tower clearly, but this time I was able to understand that the guard was eating food. The window, for some time, continued to be brighter. After 15 minutes, it went back to the usual level of luminosity. I fixed my viewfinder on that window and went to the fridge to grab some food, there I had some "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1152"}},[_vm._v("pasta")]),_vm._v(" leftovers from the day before. It was 9:05 when I started to eat, I was close to seeing the patrol. Since I fixed my focus on the entrance tower, I realized that patrol guards dropped by the window and were chatting. These dialogues might have been escaped my attention the previous days. Most parts of the guards’ bodies were covered from the outer wall, and the only reason that I was able to see the smallest movement there was that my focus was staying fixed. At 10:00 p.m., I didn’t see anyone but the guards patrolling. As time goes by, I moved my focus to the textures of the buildings. I was wandering around the texture of the roof, connection corridors and cells, surrounding the whole environment. In the maximum zoom of the lens, you would easily lose any information about the scale. Washed by the moon and spot lights, the roof lost its dimension and transformed into mere monochrome graphics. Like an endless graphic, the roof could well stretch from out of my scope to my flat, and from there, to the sky. While I could not see anyone around, I got lost in any texture that I was able to focus. "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_1641"}},[_vm._v("All night long")]),_vm._v(" examining the textures, I saw a car using the subway route at 3:00 a.m. The vehicle connected to a trailer from its back was full of white sacksAs warning lights of the two cars accompanying the process were blinking, all the vehicles stopped. They waited on the yard connecting the subway route to the highway. Approximately 5 minutes later, a pick-up truck parked parallel to the subway-car. The workers getting out of the cars, started to load the truck with sacks. It was obvious that the sacks were heavy from the body movements of the workers. Aligned in a line, the workers continued loading by transferring the load to the worker next to. I thought it would be a long process because of the time that each sack took. I had no idea what was carried in these sacks, I only knew that they were heavy. For a moment, everything in my scope were washed by the color red; the sacks, the clothes of the workers, pavements, manhole covers – the constant "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3428"}},[_vm._v("red")]),_vm._v(" light for pedestrians never changed. The frozen red light allowed ceaselessly to the cars due to the non-existent pedestrian traffic all night. When I took a step back from my viewfinder, I realized that the asphalt was shining green all along. At 4:15 a.m., the loading ended, I was able to clearly hear the sound of what I had been watching directly, there was no other sound to fill the huge emptiness between us, each echo and tingle belonged to my focus. ")],1),_c('AmpersandSeparator'),_c('div',{attrs:{"id":"section-5"}}),_c('span',[_vm._v(" After the loading I watched on the subway road at midnight, I felt sleepy. After a sleep of 2 hours, I woke up at 7:00 a.m. It took a rather long time for me to wake up completely because of the lack of sleep I had for the last four days. Drops of water were covering the glass surrounding all the façade. I opened to see outside clearly and only then I was able to understand the intensity of the rain. During my time in that flat, this was the most intense I had witnessed. It was 7:20, it made me feel good to be away from the temperature I had been sleeping in while the room got colder and colder due to the incoming cold from the open windows, I wore a jumper and started to move around my focus. The prison was calm, the number of cars on the roads started to increase. For an hour, the traffic got more intense. At 8:20, two people entered via the building door with plastic bags in their hands, they walked fast because of the rain. It was the shift change for employees or those whose duties came to an end. At 9:00, two other people entered also with plastic bags, the rain was getting thicker. The sound of rain coming through the window I had opened to capture a view was dominating all the other sounds and the think drops of rain added a texture to the image, I almost "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2709"}},[_vm._v("did not hear")]),_vm._v(" any car noises from the traffic. At 10:30, the car entrance door opened and garbage truck came in as usual, due to the rain, the garbage was carried to the covered area just in front of the transfer area. I thought I would need a good protection to get on the terrace. The garbage truck started loading after some maneuvers to come closer to the trash. The plastic garbage bags shone with the rain and each drop could be seen through my scope with the sparkles on the bags that did not give any information about what was inside. After a loading of 15 minutes, the truck left. At 10:45 a.m., I saw two vehicles bearing the prison logo on the highway in front of me. Each time I saw these vehicles did not mean that they would enter via the door I was watching. Located in various positions, the car entrance gate turned each of these cars I saw on the highway into a possibility. At 11:05, three guards were on the patrol route, the intensity of the rain didn’t affect the touring rhythm of the guards and the time of the patrol. All were protected by their uniform well. I didn’t see any one yet in the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2826"}},[_vm._v("courtyards")]),_vm._v(". At 11:30 a.m., the ambulance entered, one guard went to the ambulance, he talked to the driver for a minute and the ambulance left, no transfer was done. I couldn’t recognize the guard who went to the ambulance because he was wearing a hood. Same ambiguity applies to the highway too, I could only follow people walking on the pavements through the colors and patterns of their umbrellas. Hundreds of moving circles changed place, got on top of each other and hit each other. Umbrellas of the same color which I encountered many times might confuse me easily. I was not able to see the expressions and faces of the people I focused, I could only tell whether they could walk fast or slow. After watching the street for a while, I started to prepare my breakfast. I made a quick sandwich and got next to the window. I put on the hood of my jumper because of the cold coming from the open window. At 11:47, a visitor group of 3 entered through the outer door and ran to the building fast. The papers they had in their hands were wet and changed shape. I wasn’t able to record them since they were running, but they were a group of mother, father and child, the child must be between 16-17. They headed towards the entrance with the wet paper they were holding, and entered after cleaning their shoes on the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2923"}},[_vm._v("red doormat")]),_vm._v(". It was 12:26 p.m., the guards were at the center of the watch towers because of the rain. Some towers had two, but some had one guard. Since I saw an activity on the window of the second tower, I moved my focus "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_2924"}},[_vm._v("to the tower")]),_vm._v(". The lone guard on the tower was dancing. Due to the decreasing signal of the radio probably, he kept dancing while changing the direction of the radio’s antenna. For someone looking from below, it was almost impossible to see the guard above due to the distance and the heavy rain. The guard’s dance reminded me of something I felt while walking with an umbrella on a rainy day, generally I feel more sassy walking with an umbrella, a sassiness that allows me to express myself without thinking. The major reason of this confidence was that both of us were surrounded by a sea of low intensity. I was dancing during my time at that flat, because I was watching the surroundings my dance area was limited around the window. The guard’s dance lasted for 10 minutes, and after arranging something in the radio, he sat on the chair. Right after the guard sat down, I saw a bundled up inmate getting out to the second courtyard to smoke. I remembered him among the people gathered around the courtyard table two days before, I might have seen him for about 40-50 seconds. After finishing his cigarette, he entered in quickly. At 1:02 p.m., I saw a military shuttle at the gate, from its windows, it was seen that it was full. Just like the garbage truck, it came closer to the transfer area. 7 blue-uniformed guards got off, I thought I knew two of them. When the car arrived, one of the guards on the patrol ran to greet the vehicle. He looked like the guard told off the day before because he had worn a hood. Some of the guards got off the car entered to the building and some started to talk under the rain. 5 minutes later, the ambulance that came at 11:30 a.m., got closer to the military shuttle, two cars standing next to each other blocked my view like a wall. I could see someone with a gray sweater waiting at the door. 5 of the 7 guards went in to the military shuttle with the middle-aged inmate wearing a gray sweater. Someone with a black puffy coat was taken to the ambulance, I had never seen two people accompanying him in the courtyards. Military shuttle followed the ambulance on its way to the outside. I had prepared to go to the terrace, I found a plastic bag and cut it in a way that the lens could fit. While preparing to go out, I noticed some noises from the stair well, I looked through the door viewer. The view frame of the door viewer was no different than the one I was looking through for the last five days. With my quietness and immobility at the door, I didn’t feel anything different than what I had been doing for days. The source of the noises coming from the stair well was the apartment cleaning. Three janitors with gloves in their hands were using different cleaning tools. One was pouring soap and water from a washbowl, another with a cleaning machine was passing over it and the last person was mopping the floor. The colors of their clothes were similar to uniforms I had encountered through my scope. As the cleaning machine came closer to the "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0000"}},[_vm._v("apartment door")]),_vm._v(" the odor of soap seeped under the door and surrounded the hall I was in. After the cleaning ended, I would take the stuff I had prepared and go to the terrace. At 1:30 p.m. I was on the terrace after the cleaning. The rain was heavy, the plastic bag I cut gave me the required protection for that moment. Approximately 10 minutes after I set up, a gray civilian car entered inside, I hadn’t seen any civilian cars entered before. The person who got off the car took out paper towels wrapped in plastic from the rear seats. At that moment, a door close to the transfer area was opened and an employee with black sweater got out and helped the man carry the towels. That door had never been opened before, I thought that this door might be related to the part where some of the employees of the prison were staying in. After the paper towels were carried, the car left. At 2:10 p.m., the brown-sweater inmate who took care of the flowers in the first courtyard took a walk. Although I originally thought that he had not taken a walk the day before because of the weather conditions, this day it was raining heavier. The plants at the center of the courtyard had all dropped their leaves, and kept dropping with the wind and the rain. The trees on the hill behind the highway had almost no leaves. The plants at the center shared the information of the outside to the inside and the information of the inside to the outside, by preserving their rhythm as every "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1301"}},[_vm._v("tree")]),_vm._v(" on earth. I thought that it was sufficient to look at one in order to understand their situation. While focused at the center of the first courtyard, I saw someone heading to the front yard from the pedestrian entrance. He was wearing a long coat and black trousers. It was someone who cared for his clothes in model and proportion. With his "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2612"}},[_vm._v("black umbrella")]),_vm._v(" he walked slowly towards the building door. With one of his hands he was holding the umbrella and the other was in his pocket, I wasn’t able to tell whether he was holding any papers. I don’t know why but I didn’t think he was a visitor; he wasn’t stopped for a check at the security cabin. At 2:25 p.m., there were simultaneous entries both to the seventh and eighth courtyards. Two friends walking in the seventh and the inmate running in the eighth came back. The friends in the seventh were walking faster, after five minutes, they went back inside not to get wetter. The cover that the roofs of the courtyard provided was not much, therefore people I saw did not spend too much time outside and went back quickly. The runner inmate of eighth looked like he wanted to get a bit of fresh air, he watched the sky for some time and went inside. After the inmates in the courtyards went inside, I didn’t see anyone else. It was 3:17 p.m., a white van entered, the one who greeted the car was the employee who had come out from the annex door before, he accompanied the guard and headed towards the transfer gate opening to the building. The patrolling young guard who generally accompanied the transfers came swiftly next to the car. The transfer door was open and the car was not blocking my view of the inside. When I focused on inside, I saw the granite tile floor which was lit by white fluorescent light. The young guard who finished talking with the employee inside got on the car and the van left the premises. The one who got out of the van closed the transfer door. When the door was closed, I started to hear some rattle around me, this sound came from the locked door of the terrace, which I didn’t use before. I quickly turned my scope to somewhere else and the door was opened. Five people or so came to the terrace, one of them was the superintendent wearing a suit. They asked me what I was doing there. Beforehand, having thought that this could happen, I prepared something to say. After I explained myself, the superintendent said that they were there for cleaning and a general check of cleaning on terrace. Upon my explanation he offered that he could ask the site management for a permission to use my scope more. I replied that it would be expected that nobody would want to take responsibility for such a permit and it would be “denied” easily. One of the janitors who came later said “that’s no good” upon seeing the terrace. More or less, I guessed what would follow. While they were talking on the phone, I started to pack and got ready to leave, I didn’t want to give any information, at least not to take this problem to my flat. While I was saying goodbye, the superintendent on the phone asked me my flat number, I said some other unrelated number and went back to my flat, they didn’t ask anything else. I noted this hour in addition to the regular cleaning hours, I could be more careful when on terrace around this hour. The next day would be my last day. I thought I could spend all my time until checkout at the flat, I didn’t want to be visible after such a dialogue I had. At 4:10 p.m., it was 10-15 minutes after I had arrived at the flat, taking a look from my scope after the encounter on the terrace calmed me down. For a while, I didn’t see anyone in the prison, so I opened my computer to continue backing up the images I backed up every night. The battery was low in my computer and I realized my charger wasn’t working, I had seen that the cable was worn down but this was the first time it had a malfunction. For a while, I didn’t know how to transfer my files, then I started to play with the cables of the charger. With a similar craftsmanship to focusing the scope, I held the copper wires inside the cable at a certain angle, I taped them, then pushed them against the wall and fixed them at a point where I could see the charger light was on. The charger was working and I wasn’t planning to touch the cable until the day after. At 4:30 p.m., two visitors exited, after what happened on the terrace, I might have missed their entrance. The rain got less heavy but still intense. The puddles of water on the highway got deeper, the soil parts of the construction site in front of me transformed into puddles of mud and slowly, seeped through the gaps at the construction’s foundation. In the second courtyard, I realized that young inmate with gray turtleneck went outside to smoke before it got dark; this was the same inmate who toured in the courtyard staying very close to the wall two days before and he was alone. I generally saw the inmates in the second courtyard in groups, it was only this young inmate who took to the courtyard at certain hours. Just a short while before, the chairs in the fifth courtyard were taken inside by someone wearing a navy blue jumpsuit. In the courtyards, all the cushions over the chairs had turned color by the rain. As it got closer to 5:00 p.m., two attendants wearing luminous "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2640"}},[_vm._v("uniforms")]),_vm._v(" caught my attention. As I zoomed in, I saw a thin metal frame that the attendants lifted by getting support. I didn’t understand what it was, it was too big for a manhole cover. When one of them stepped aside, I saw a hole cut in the middle of the frame, that the gutter on the edge of the road could fit. It was a frame that protected the gutter from pieces that could plug it up. The attendants installed the frame and then, got in their car and drove away. It was getting dark and the rain was falling heavier again, it was imminent that the lights of both side would turn on. I was hungry, I totally forgot that after the dialogue I had. I took some canned tuna and some bread and got next to the window. At 6:45 p.m., 2 people left with plastic bags in their hands. Mostly I could not recognize employees going in and out, but this time I was able to recognize the healthcare and cleaning workers I had seen two days before during transfer without their uniforms on. They headed quickly towards the door at the car entrance. The lights of the watchtower at the entrance turned on simultaneously. After a short time, I saw two employees passed through the outer door. At 7:05, I fixed my focus on the furthest watch towers. Because they were far, my scope was able to cover them as a whole, the only data I could take note of about the guards inside was the number of them. It was expected that things I noted down were mostly quantitative given the distance, whatever I was seeing transformed into similar volumes. The wind started to shake even harder the viewfinder focused on the distant. The farthest tower had two people, the one before that had one. Since there were spotlights on the tower in the middle, I could see the writings on the white wall of the interior tower as blurry smudges. These writings that followed each other as long sentences, mostly were closer together towards the edges of the wall and stretched along the both directions following the corners of the tower. At 8:15 p.m., the patrol guards were at their usual spot, the inscription “gendarmerie” on their backs shone even brighter under the spot lights. I moved the center of my focus to the light source. The horizontal intense cluster of light at the center of my circular view frame tired my eyes with their brightness and made the screen of my phone which I connected for documentation white; the white screen illuminated for a moment the major part of the dark room I was in. The light maintaining the control there continued its duty by extending it to my room, and transformed the room I was in into a secondary area to be "),_c('inline-image-link',{attrs:{"image-i-d":"MOV_3039"}},[_vm._v("looked out for")]),_vm._v(". Hastily I moved my focus and kept watching the patrol route. At 8:42 p.m., I saw shadows on one of the windows connected to the first courtyard, there was a possibility that these windows were not opening to the wards but to the corridors in between. This was an educated guess that I made considering the shape of the roof. For 5 minutes, I saw human shadows come and go at least for 3-4 times, later, I didn’t witness any activity. While looking at the window with the light on, I heard the car noises more than usual; these were the hours after the rush hour, when there was no crowd, and cars move frequently and fast. The cars running over the puddles of water after the rain, enhanced the noises coming from the highway. For a while, I moved my focus around the puddles. At 9:45 p.m., the sliding car gate was opened, it was a rare thing for cars to enter at that time of the night. The car entered looked like a school bus, after the door was opened, it moved to the front yard. Normally, all the cars would move to the transfer area but this one got closer to the stairs where the building entrance of the front yard was located, in the meantime, the light seeping through the entrance door was reflected from the floor underneath the doormat. After the entrance door was opened, one person ran from the inside and "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3251"}},[_vm._v("got on the shuttle")]),_vm._v(", the door was still open. 2 minutes later 2 people got on the shuttle. One of them was the one who took care of the statue the other day. It was understood that the shuttle entered this area in the yard because of the weather conditions, no vehicle had ever used this route before. After everyone got on, the shuttle exited. At 11:10 p.m., the patrolling guards came across each other, they greeted and moved in opposite directions. In a short while, the light of the window on the corridor connecting the entrance building and the ward turned on, it took me long time to understand that there were long and thin wardrobes inside, because of the dark – it looked like a locker room. After the light went out, I moved my focus to the front yard, the razor wire washed with water coalesced with the spot light at the center of the prison and shone to my lens. The brightness of the metal wire over the walls, spiraled all along the wall and exhibited itself as a giant crown surrounding the building. The razors on this thin metal were "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3395"}},[_vm._v("the whitest")]),_vm._v(" lights shining to my lens. I turned to look at the construction site separated by the overpass, and saw that all pits became whole lakes by the rainwater. Soil and sand piles on the floor of the construction site transformed into a large swamp and compelled the security guards to stay at their cabins. I would be leaving the afternoon after and that was my last night there, I wanted to stay awake the whole night. The quietness of the night there resembled the quietness of my time at the flat that I stayed the whole day. Every sound I made at my flat, sprang forth and the sounds of the outside directly came to my flat. Like a vibration felt in the same scale and on a singular floor; the emptiness of the flat, the roads and the courtyards coalesced into one. I could pay attention simultaneously to the light in the courtyard, the can dragging on the highway and the sound of the refrigerator. At 5:15 a.m., the light of the window on the courtyard was turned off. ")],1),_c('AmpersandSeparator'),_c('div',{attrs:{"id":"section-6"}}),_c('span',[_vm._v(" The rain stopped at 7:22 a.m. and it started to dawn. The sharp contours and the resolutions of the objects washed by the rain appeared on my focus, I could understand the scale of the dust fallen above them via the vibrant colors that only then caught my eye. At 8:12 a.m. a black-uniform officer entered through the outer door, he greeted the guardian on the watch and went into the building through the transfer door. At 9:05 a.m., an ambulance came inside, after the back doors were opened, four guards with blue uniforms got off, the guard on duty at the outer door joined them. 15 minutes after the ambulance arrived, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0853"}},[_vm._v("a second ambulance")]),_vm._v(" arrived at the door, the guard on duty returned to his cabin to open the door. The newly arrived ambulance parked in parallel to the transfer area and opened its doors, two blue-uniform guards got off. After the guards separated into two groups, they waited for some time. 9:25, the inmate wearing yellow trousers got on the ambulance closest to the door. After the transfer, ambulance kept waiting. Three minutes later an old person left through the transfer door slowly, the guards helped the inmate along the way. During my time here, he was one of the oldest inmates I had seen, he sat in the second ambulance with the help of the guards. For two ambulances, there was no emergency, nobody rushed or needed sirens. Some of the guards got on the ambulance to accompany the old inmate, the others waited in the garden. After the door was opened, the ambulances left. At 10:10 a.m., the garbage was ready and waiting, on an earlier time than usual. "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3631"}},[_vm._v("Brooms and dustpans")]),_vm._v(" in the building were taken out and put in an order along the wall. Each were washed and left to dry under the sun. 15 minutes later the garbage truck came in. It was waiting in parallel to the exit door, while the garbage bags left at the corner were carried, new bags started to come. The garbage taken out that day were like numerous waste of a general cleaning, opaque black bags made it impossible to see through. In my trash can, there were empty cans, boxes of pizza and burned eggs. Since the garbage truck parked in parallel to the door, I was able to examine the inscriptions and graphics on it; on the edges of the green-painted surface were the pink flower designs and at the center, “Clean as Your House” written in white. After the 25-minute garbage disposal, five employees carrying the garbage bags, took the cleaning gear leaning on the wall and went inside. The garbage truck started to press the garbage by using the pistons inside the garbage reservoir, and demolished all the garbage filling its reservoir with a squeal that reached even to my ears. By that time, the scale of the garbage became ambiguous as the bags which I couldn’t see through. At 11:10 a.m., while waiting for the patrol, I realized nobody was passing by, I thought that the patrol which I regularly observed, was hindered. While waiting fixed on the patrol route, at 11:22 a car passed through my focus. I had never seen a car on this route before. The patrol route between the inner and outer walls was not wide, the car was using this route carefully and slowly. At 11:50 a.m., the middle-aged inmate with maroon sweater appeared in the third courtyard, he was walking with his hands in his jeans’ pockets. During this time of cold, I started to encounter people wearing jeans more often. After walking 10-15 minutes, he wiped off the droplets on the chair in the courtyard and sat on it. For a long time, he examined the building I was in and the other buildings of the same residence complex. During the same duration, three people got out to the first courtyard, the long-haired blond I saw the first day and black-haired young inmate I remembered from the conversation around the table from two days before started to chat. The brown-sweater inmate who took a walk every day, was accompanying both of them. Before sitting, for a while, 3 people chatted together. The young ones took two chairs next to each other, the brown-sweater inmate started to tour the courtyard slowly after closely examining the leaves fallen during the heavy rain of the day before. This time, "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_1269"}},[_vm._v("I didn't see")]),_vm._v(" the two friends walking along the seventh courtyard all the time. At 12:16 p.m., the transfer door was opened, two black uniform officers got out, one had white gloves in his hands. After getting a bit far from the door they had left conversing, the stopped and continued to talk, they were carefully listening to each other. Their conversation took a 15 minutes in front of the transfer area. After the dialogue ended, I realized that ones in the courtyard had already gone in. I moved my lens to the patrol route, what I was wondering was whether I could see any cars on the patrol route again. As I fixed my lens on the patrol route, a cardboard box fell on my view frame from the outside, it stopped as a brown stain on the gray concrete of the patrol route. At 12:20, I saw the patrolling guards, this was approximately ten minutes late. As I zoomed in the guardians, I realized that they were high ranking officers from their uniforms. While walking between first and second towers, they passed over "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3780"}},[_vm._v("the cardboard box")]),_vm._v(" and kept walking. At 12:50 p.m., two people I hadn’t seen before in the fifth courtyard sat around a table and started chatting, one had a long, gray beard and the other had short black hair. The long-bearded inmate said at most a sentence during the whole conversation. The short-haired went inside after finish what he had to say. The long-bearded inmate watched around for a while, and went back inside after smoking a cigarette. After the tranquility ruled again, I reminded myself about eating something. I grabbed cheese, olives and honey from the fridge, the bread was stiff after six days, I heated up the breads and then, positioned myself next to the window again. At 1:15 p.m., the entrance door opened, the personnel talking on the phone while leaning on the opening door kept talking on the same area. Probably due to the cold outside, he was waiting in between, without completely getting out. While watching him talking on the phone, I heard a rumble. The employee talking startled all of a sudden, the noise was coming from the construction site that was adjacent to the overpass. A huge crane was trying to extract metal construction material from a pit that was dug for the foundation. Everyone with open windows in the courtyard must have heard this noise. During this process which lasted approximately 10 minutes, the metal piece was taken out while scraping on the perimeter of the pit, surrounded by concrete. At 1:55 p.m., the entrance door was opened again, this time, janitor got to the area where the stairs were located, he put all the cleaning equipment next to each other and rolled some of the red doormat. When the cleaning started, I remembered that I had to leave the flat and I needed to leave it clean. Since I did my observations both from the living room and the bedroom, I had completely altered the positions and the usages of the furniture. Boxes of stuff that were put on top of each other became an altar for my journal while some of the chairs were transferred into mere elevations which I got on, and arrange my view frame. The coffee table and the couch on which I kept observing while eating were adjacent to the window. Instead of a series of furniture positioned as opposed to each other in a circular form like you can find in a house, all furniture was facing the windows in a linear form, resembling a series of seats aligned for a performance. After recording the cleaning process, I started to tidy up the flat. At 2:30 p.m., after packing up, the clouds had already went away, the sun started to show its face to the courtyards and the garden. In the second courtyard, the youngest of the inmates, the one wearing a gray sweater, walked towards the sunny part of the courtyard and waited for a while leaning on the wall. At the same time, the inmates I had seen playing ball in the third yard on my second day were sitting facing the sun, chatting. After the conversation the one with orange t-shirt hung a rope across the courtyard and strung out "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_0995"}},[_vm._v("the trousers")]),_vm._v(" he brought from the inside. His trousers looked like a floating sign on my lens, as I zoomed in I got to see its scale and texture. After those in the courtyard went inside, the trousers started to sway with the wind. I was about to leave, I moved my focus to a truck stopped at the edge of the road. The orange canvas on the truck was opened to carry some materials to the construction site nearby and most of it was slopped over the highway. This huge orange stain was covering one of the lanes on the road, the cars were turning on their signal to change the lane. During this short hubbub, I was able to hear all the horns from my flat. The driver ran to the road to "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_3847"}},[_vm._v("the canvas")]),_vm._v(" up. After the truck was covered, it was nothing like before. I checked the positions of the furniture in my flat again. When I returned to the scope, I moved my focus once again for the last time, for a moment, the shining lightning rod installed on the roof of the prison caught my attention; before getting ready to leave, I fixed my focus one last time. I picked up my scope and packed and got on a taxi. The lightning rod was still on my mind, during all the time with bad weather, I hadn’t witnessed any lightning, maybe the lightning rods in my building were quick to draw the lightning over. In this formula evaluated by the physical height, the height of the building I had been in and the height of the surrounding buildings did not match. It was certain that everything above me would draw the whole energy over, and at the same time, be an open target for the sky. The only thing that distinguished this remarkable brightness from a disaster was a pointed "),_c('inline-image-link',{attrs:{"image-i-d":"IMG_2777"}},[_vm._v("piece of metal")])],1)],2)}
